import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import DialogTitle from "@material-ui/core/DialogTitle";
import {DialogContentText, Link, TextareaAutosize} from "@material-ui/core";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Input from "@material-ui/core/Input";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Languages from "../utils/Languages";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import {withTranslation} from "react-i18next";
import Helpers from "../utils/Helpers";
import Consts from "../utils/Consts";
import UploadQueueFifo from "../repos/UploadQue";
import UploadFileJob from "../model/UploadFileJob";

// The strings are defined by google:
const FILE_TYPE_REGULAR = "default";
const FILE_TYPE_PHONE = "phone_call";
const FILE_TYPE_VIDEO = "video";
const MAX_FILE_SIZE_IN_GIGABYTES = 0.5;

const secondsToMinutes = seconds => Math.floor(seconds / 60) + ':' + ('0' + Math.floor(seconds % 60)).slice(-2);

const styles = theme => ({
  root: {
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  hidden: {
    display: "none",
  },
  formControl: {
    display: "block",
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
  urlInput: {
    border: 'solid 1px gray',
    width: "100%"
  },
  actionsContainer: {
    textAlign: "right",
  },
  selectFileButton: {
    cursor: "pointer",
    color: theme.palette.primary.main
  }
});

class UploadDialog extends React.Component {

  componentWillReceiveProps(nextProps, nextContext) {
    if (nextProps.creditsInMinutes!=this.props.creditsInMinutes) {
      this.calculateMissingCredits(this.state.durationInSeconds, nextProps.creditsInMinutes);
    }

    if (Languages.isLanguageCodeSupported(nextProps.prefLanguage)) {
      this.setState({languageCode: nextProps.prefLanguage});
    }

    if (nextProps.prefNumSpeakers) {
      this.setState({numSpeakers: nextProps.prefNumSpeakers});
    }
  }

  defaultModel = FILE_TYPE_VIDEO;
  TOTAL_NUMBER_OF_STEPS = 3;

  state = {
    //filesToUpload: [], // TODO... replace fileUrl, file & duration in Seconds with this list. => have class getters that aggregate data from this state's list
    fileUrl: "",
    urls: [],
    file: null,
    files: [],
    languageCode: (this.props.prefLanguage && this.props.prefLanguage!="" && this.props.prefLanguage!="null" && this.props.prefLanguage!="undefined")  ? this.props.prefLanguage : "en-US",
    numSpeakers: (this.props.prefNumSpeakers && this.props.prefNumSpeakers!="" && this.props.prefNumSpeakers!="null" && this.props.prefNumSpeakers!="undefined")  ? this.props.prefNumSpeakers : 1,
    model: this.defaultModel,
    durationInSeconds: null,
    missingCreditsInMinutes: 0,
    activeStep: 0,

  };


  componentDidMount() {

  }

  modelToLabel = (model) => {
    const {classes, t, i18n } = this.props;

    if (model==FILE_TYPE_PHONE) {
      return t("upload.Phone call");
    }

    if (model==FILE_TYPE_REGULAR) {
      return t("upload.Exceptional Speech Clarity");
    }

    if (model==FILE_TYPE_VIDEO) {
      return t("upload.Regular");
    }

  };

  handleFileChange = (e) => {
    console.log("file changed!");
    const files = e.currentTarget.files;
    if (files == null || files.length==0) {
      return;
    }

    //this.removeAllAudioTagsFromDom(); // TODO...
    this.urlInputRef.value = "";

    let kosherFiles = [];
    let nonKosherFiles = [];

    for (const file of files) {
      if (file.size < MAX_FILE_SIZE_IN_GIGABYTES * 1024 * 1024 * 1024) {
        kosherFiles.push(file);
      } else {
        nonKosherFiles.push(file);
      }
    }

    if (kosherFiles.length==1) {
      var file = kosherFiles[0];
      this.setState({files: kosherFiles, file: file, fileUrl: "", urls: [], durationInSeconds: 0});
      let self = this;
      for (const file of kosherFiles) {
        let fileURL = URL.createObjectURL(file);
        self.audioRef.setAttribute('src', fileURL);
        /*var reader = new FileReader();
        reader.onload = function (e) {
          self.audioRef.setAttribute('src', e.target.result);
          console.log("audio duration: " + self.audioRef.duration);
        }
        reader.readAsDataURL(file);*/
      }
    } else {
      this.setState({files: kosherFiles, file: kosherFiles[0], fileUrl: "", urls: [], durationInSeconds: 0});
    }

    if (nonKosherFiles.length>0) {
      let names = "";

      for (const file of nonKosherFiles) {
        if (names) {
          names += ", ";
        }
        names += file.name;
      }

      alert ("We cannot upload " + nonKosherFiles.length + " files as they are too large. The files are: " + names + ". See our minimization tools to reduce their size: https://speechnotes.co/files/minimize_files/");
    }

  };

  removeAllAudioTagsFromDom = (e) => {
    // TODO...
  }

  handleFileUrlChange = (e) => {
    // https://ttsreader.com/actors/1001/sample1.mp3
    let csUrls = e.target.value;

    if (csUrls==null || csUrls==undefined) {
      return;
    }

    this.fileInputRef.value = "";
    csUrls = csUrls.trim();

    let urls = csUrls.split(",");
    let realUrls = [];
    let respectiveEmptyFiles = [];

    for (let url of urls) {
      url = url.trim();
      if (url.length>4) {
        if (!url.startsWith("http://") && !url.startsWith("https://")) {
          url = "https://" + url;
        }
        if (url.startsWith("https://drive.google.com/")) {
          url = Helpers.getDriveDownloadableFromLink(url);
        }

        realUrls.push("" + url);

        let potentialNames = url.split("/");

        while (potentialNames.length>0 && potentialNames[potentialNames.length-1].length==0) {
          potentialNames.length = potentialNames.length-1;
        }

        let name = "";

        if (potentialNames.length>0) {
          name = potentialNames[potentialNames.length-1];
        }

        if (name=="") {
          name = "online_file";
        } else if (name.startsWith("uc?export=download&id=")) {
          name = "driveId=" + name.substring(22);
        }

        let emptyFile = this.buildEmptyFile(name);

        respectiveEmptyFiles.push(emptyFile);
      }
    }

    if (realUrls.length>0) {
      if (realUrls.length==1) {
        this.audioRef.setAttribute('src', realUrls[0]);
      }
      this.setState({file: respectiveEmptyFiles[0], files: respectiveEmptyFiles, fileUrl: realUrls[0], urls: realUrls, durationInSeconds: 0});
    }

  };

  buildEmptyFile = (fileName) => {
    var parts = [
      new Blob([''], {type: 'text/plain'}), '',
      new Uint16Array([33])
    ];

    // Construct a file
    fileName = (fileName!=null && fileName!=undefined && fileName.length>0) ? fileName : "web_file";
    let file = new File(parts, fileName, {});

    return file;
  };

  handleDurationChange = (e) => {
    //console.log("can play! ", e.currentTarget.size);
    //add duration in the input field #f_du
    let fileDuration = Math.round(e.currentTarget.duration);
    if (fileDuration==0) {
      fileDuration = 1;
    }
    //URL.revokeObjectURL(this.obUrl);
    let totalDuration = this.state.durationInSeconds + fileDuration;
    console.log("can play! duration = ", fileDuration);
    this.setState({durationInSeconds: fileDuration});
    this.calculateMissingCredits(fileDuration, this.props.creditsInMinutes);
  };

  calculateMissingCredits = (f_duration, creditsInMinutes) => {

    console.log("credits = " + creditsInMinutes);

    if (creditsInMinutes!=null && creditsInMinutes!=undefined) {
      let creditsInSeconds = creditsInMinutes*60;
      let missingCreditsInSeconds = f_duration - creditsInSeconds ;
      console.log('missing credit = ' + missingCreditsInSeconds);
      if (missingCreditsInSeconds > 0) {
        let missingCreditsInMinutes = Math.ceil(missingCreditsInSeconds/60);
        this.setState({missingCreditsInMinutes: missingCreditsInMinutes});
        return;
      } else {
        this.setState({missingCreditsInMinutes: 0});
      }
    }
  };

  handleLanguageChange = event => {
    this.setState({
      languageCode: event.target.value,
    });
  };

  handleNumSpeakersChange = event => {
    this.setState({
      numSpeakers: event.target.value,
    });
  };

  handleModelChange = event => {
    this.setState({
      model: event.target.value,
    });
  };

  reset = () => {
    if (this.fileInputRef!=null) {
      this.fileInputRef.value = "";
    }
    if (this.urlInputRef!=null) {
      this.urlInputRef.value = "";
    }

    this.setState({
      file: null,
      files: [],
      fileUrl: "",
      urls: [],
      languageCode: Languages.isLanguageCodeSupported(this.props.prefLanguage) ? this.props.prefLanguage : "en-US",
      numSpeakers: this.props.prefNumSpeakers ? this.props.prefNumSpeakers : 2,
      model: this.defaultModel,
      durationInSeconds: null,
      missingCreditsInMinutes: 0,
      activeStep: 0,
    });
  };

  handleNext = () => {
    this.setState({
      activeStep: this.state.activeStep+1,
    });
  };

  handleBack = () => {
    if (this.state.activeStep==0) {
      return;
    }
    this.setState({
      activeStep: this.state.activeStep-1,
    });
  };


  onSubmit = async (e) => {
    if (this.state.files==null) {
      return;
    }

    let files:File[] = [ ...this.state.files];

    if (files==null || files.length==0) {
      return;
    }

    let urls:String[] = [];
    let isLinks = false;
    if (this.state.urls!=null && this.state.urls.length>0 && this.state.urls.length==files.length) {
      urls = [ ...this.state.urls];
      isLinks = true;
    } else {
      for (const file of files) {
        urls.push("");
      }
      isLinks = false;
    }

    if (!isLinks) {
      files.sort(function (a, b) {
        return a.size - b.size
      });
      console.log('sorted files: ', files); // Sort by size. Small first.
    }

    //  TODO: Hook uploading UI - which was:  this.props.onStarted();  directly to queue <-> redux
    for (let i=0; i<files.length; i++) {
      let file = files[i];
      let url = urls[i];
      UploadQueueFifo.push(new UploadFileJob(file, url, file.name, this.state.languageCode, this.state.model, this.state.durationInSeconds, this.state.numSpeakers));
      console.log("uploading: ", file, url, file.name, this.state.languageCode, this.state.model, this.state.durationInSeconds, this.state.numSpeakers);
    }
    //Api.uploadFile(this.state.file, this.state.fileUrl, this.state.languageCode, this.state.model, this.props.onSuccess, this.props.onFail, this.props.onProgress );
    this.reset();
    this.props.onClose();
  };

  getFilesDescription = (t) => {
    return this.isFileLegal() ?
      (this.state.files.length==1 ? t('upload.Selected File') + ": " + this.getFileDisplayName() : "" + this.state.files.length + " files selected: " + this.getFileDisplayName() + " + " + (this.state.files.length-1) + " more. " )
    + " " + this.getDurationAsString(t)
  : "";
  }


  getDurationAsString = (t) => {
    if (this.state.durationInSeconds>0) {
      return t('duration') + ": " + secondsToMinutes(this.state.durationInSeconds) + " " + t('minutes') + ".";
    } else {
      return ""
    }
  }

  isReadyToUpload = () => {
    return this.state.activeStep>=this.TOTAL_NUMBER_OF_STEPS-1 && this.isFileLegal() && this.state.missingCreditsInMinutes<=0 && this.props.creditsInMinutes>=1;
  }

  isFileLegal = () => {
    return this.state.file!=null /*&& this.state.durationInSeconds > 0*/;
  };

  isFileOnline = () => {
    if (this.state.fileUrl!=null && this.state.fileUrl!=undefined && this.state.fileUrl.length>0) {
      return true;
    } else {
      return false;
    }
  }

  isLanguageLegal = () => {
    return Languages.isLanguageCodeSupported(this.state.languageCode);
  };

  getFileDisplayName = () => {
    if (this.state.file==null || this.state.file==undefined) {
      return "";
    }

    if (this.state.fileUrl!=null && this.state.fileUrl!=undefined && this.state.fileUrl.length>0) {
      return this.state.file.name + " (online)";
    }

    return this.state.file.name;
  };

  render() {
    const {classes, t, i18n } = this.props;

    const SIZE_LIMIT_INFO = " (Up to 500MB*)"
    const currentCreditInMinutes = this.props.creditsInMinutes;

    //console.log(this.state);

    if (this.props.open) {
      return <div >
        <Dialog
          className={classes.root}
          fullWidth={true}
          maxWidth = {'sm'}
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
      >
        <DialogTitle id="scroll-dialog-title">{t('upload.title')}</DialogTitle>
        <DialogContent>

          { currentCreditInMinutes<1 ?
            <div>
              <Typography variant="subtitle1" align="center" paragraph>
                <span style={{color: "#f00"}}>{t('upload.missing credit')} {currentCreditInMinutes<1 ? "" : this.state.missingCreditsInMinutes + " " + t('minutes')}. {t('upload.please add credit')}</span>
              </Typography>
              <div className={classes.heroButtons}>
                <Grid container justify="center">
                  <Grid item>
                    <Button variant="outlined" color="primary" onClick={this.props.openPurchaseDialog}>
                      {t('hero.Add credit')}
                    </Button>
                  </Grid>
                </Grid>
              </div>
              <br/>
            </div>: <div/>
          }

          <Stepper activeStep={this.state.activeStep} orientation="vertical">
              <Step key="0">
                <StepLabel>{this.state.activeStep<=0 ? t('upload.Select Audio or Video File') : t('upload.Selected File') + ": " + this.getFileDisplayName() }</StepLabel>
                <StepContent>
                  <FormControl className={classes.formControl}>
                    <input type="file" ref={ref => this.fileInputRef = ref} multiple={true} className={classes.hidden} name="file" id="file" onChange={ (e) => this.handleFileChange(e) } accept={Consts.SUPPORTED_FILES}/>
                    <label style={{border: "1px solid #3f52b5",padding: "2px 10px", width: "100%"}} htmlFor="file" className={classes.selectFileButton}>{(this.state.file==null || this.isFileOnline()) ? t('upload.Select file') + SIZE_LIMIT_INFO : "Selected: " + this.state.file.name}</label>
                    <p style={{marginTop: "10px", marginLeft: "10px", textAlign: "right"}} >*Need to reduce file size? <a target={"_blank"} href={"https://speechnotes.co/files/minimize_files/"}>Click here</a></p>
                  </FormControl>
                  <br/>
                  Online file or YouTube? No need to download - we only need the link.
                  <br/><br/>
                  <FormControl className={classes.formControl}>
                    <TextareaAutosize aria-label="minimum height" rowsMin={1} className={classes.urlInput} placeholder="Paste url here. Accepts multiple comma separated urls." name="fileUrl" id="fileUrl" onChange={ (e) => this.handleFileUrlChange(e) } ref={ref => this.urlInputRef = ref} style={{marginBottom: "10px"}} />
                    <Link style={{marginTop: "3px"}} variant={"caption"} target={"_blank"} href={"https://www.howtogeek.com/299668/how-to-create-shareable-download-links-for-files-on-google-drive/"}>* How to share a Google Drive link publicly</Link>
                    <br/>
                    <Typography style={{marginTop: "3px"}} variant={"caption"} color={"textSecondary"}>* Google Drive files limited to 100MB file size.</Typography>
                  </FormControl>
                  <br/>
                  <p style={{color: "#3f51b5"}}>{this.getFilesDescription(t)}</p>
                  <br/>
                  { (this.state.missingCreditsInMinutes > 0 || currentCreditInMinutes<1) ?
                    <div>
                      <br/><br/>
                      <Typography variant="subtitle1" align="center" paragraph>
                        <span style={{color: "#f00"}}>{t('upload.missing credit')} {currentCreditInMinutes<1 ? "" : this.state.missingCreditsInMinutes + " " + t('minutes')}.</span>
                      </Typography>
                    </div>:
                    <div/>
                  }
                  <div className={classes.actionsContainer}>
                    <div>
                      {/*<Button
                        disabled={this.state.activeStep === 0}
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        Back
                      </Button>*/}
                      <Button
                        disabled={!this.isFileLegal()}
                        variant="contained"
                        color="primary"
                        onClick={this.handleNext}
                        className={classes.button}
                      >
                        {t('next')}
                      </Button>
                    </div>
                  </div>
                </StepContent>
              </Step>

            <Step key="1">
              <StepLabel><span className={"Flag-font"}>{this.state.activeStep<=1 ? t('upload.recording language') + " & number of speakers 🧑🧑" : t('upload.recording language') + ": " + Languages.codeToLabel(this.state.languageCode) +
                ("cmn-Hans-CN,en-IN,en-SG,en-GB,en-US,fr-FR,de-DE,it-IT,ja-JP,pt-BR,ru-RU,es-ES".indexOf(this.state.languageCode)>-1 ? ", Speakers = " + this.state.numSpeakers : "")
              }
              </span></StepLabel>
              <StepContent>
                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="language-select">{t('Language')}</InputLabel>
                  <Select
                    id = "language-select"
                    value={this.state.languageCode}
                    onChange={this.handleLanguageChange}
                    input={<Input id="language-select" />}
                  >

                    {Languages.languages.map((language, index) =>
                      <MenuItem key={index+1} value={language[0]}><span className={"Flag-font"}>{(Languages.codeToFlag(language[0]))}</span>&nbsp;&nbsp;<span>{Languages.codeToName(language[0])}</span></MenuItem>
                    )}

                  </Select>
                </FormControl>

                {
                  "cmn-Hans-CN,en-IN,en-SG,en-GB,en-US,fr-FR,de-DE,it-IT,ja-JP,pt-BR,ru-RU,es-ES".indexOf(this.state.languageCode)>-1 ?
                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="numSpeakers-select">{"Speakers 🧑🧑 ?"}</InputLabel>
                  <Select
                    id = "numSpeakers-select"
                    value={this.state.numSpeakers}
                    onChange={this.handleNumSpeakersChange}
                    input={<Input id="numSpeakers-select" />}
                  >

                    {[1,2,3,4,5,6,7,8,9].map((value, index) =>
                      <MenuItem key={index} value={value}>{value}</MenuItem>
                    )}

                  </Select>
                </FormControl> : <p style={{color: "gray"}}>(Speaker-tagging under development for selected language)</p>
                }

                <div className={classes.actionsContainer}>
                  <div>
                    <Button
                        disabled={this.state.activeStep === 0}
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                      {t('back')}
                      </Button>
                    <Button
                      disabled={!this.isLanguageLegal()}
                      variant="contained"
                      color="primary"
                      onClick={this.handleNext}
                      className={classes.button}
                    >
                      {t('next')}
                    </Button>
                  </div>
                </div>
              </StepContent>
            </Step>

            <Step key="2">
              <StepLabel>{this.state.activeStep<=2 ? t('upload.recording type') : t('upload.recording type') + ": " + this.modelToLabel(this.state.model)}</StepLabel>
              <StepContent>

                <DialogContentText>{t('upload.type explanation')}</DialogContentText>

                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="model-select">{t('upload.recording type')}</InputLabel>
                  <Select
                    value={this.state.model}
                    onChange={this.handleModelChange}
                    input={<Input id="model-select" />}
                  >

                    <MenuItem value={FILE_TYPE_VIDEO}>{this.modelToLabel(FILE_TYPE_VIDEO)}</MenuItem>
                    <MenuItem value={FILE_TYPE_PHONE}>{this.modelToLabel(FILE_TYPE_PHONE)}</MenuItem>
                    {/*<MenuItem value={FILE_TYPE_REGULAR}>{this.modelToLabel(FILE_TYPE_REGULAR)}</MenuItem>*/}
                  </Select>
                </FormControl>

                <div className={classes.actionsContainer}>
                  <div>
                    <Button
                        disabled={this.state.activeStep === 0}
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        {t('back')}
                      </Button>
                    <Button
                      disabled={!this.isFileLegal()}
                      variant="contained"
                      color="primary"
                      onClick={(e) => {
                        if (this.isReadyToUpload()) {
                          this.onSubmit(e);
                        } else {
                          this.handleNext();
                        }
                      }}
                      className={classes.button}
                    >
                      {this.isReadyToUpload() ? t('next') + " & " + t('upload.upload') : t('next') }
                    </Button>
                  </div>
                </div>
              </StepContent>
            </Step>
          </Stepper>

          <br/><br/>

          <DialogContentText color={"primary"} style={{fontWeight: "bold"}}>{this.getFilesDescription(t)}</DialogContentText>

          { (this.state.missingCreditsInMinutes > 0  || currentCreditInMinutes<1 )?
            <div>
              <br/><br/>
              <Typography variant="subtitle1" align="center" paragraph>
                <span style={{color: "#f00"}}>{t('upload.missing credit')} {currentCreditInMinutes<1 ? "" : this.state.missingCreditsInMinutes + " " + t('minutes')}. {t('upload.please add credit')}</span>
              </Typography>
              <div className={classes.heroButtons}>
                <Grid container justify="center">
                  <Grid item>
                    <Button variant="outlined" color="primary" onClick={this.props.openPurchaseDialog}>
                      {t('hero.Add credit')}
                    </Button>
                  </Grid>
                </Grid>
              </div>
            </div>:
            <div/>
          }

          <audio preload="metadata" onDurationChange={ (e) => this.handleDurationChange(e) } ref={ref => this.audioRef = ref}/>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="secondary">
            {t('cancel')}
          </Button>
          <Button onClick={this.reset} color="secondary">
            {t('upload.reset')}
          </Button>
          <Button onClick={this.onSubmit} color="primary" disabled={!this.isReadyToUpload()} >
            {t('upload.upload')}
          </Button>
        </DialogActions>
      </Dialog>


      </div>
    }

    return <div/>
  }

}



UploadDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(UploadDialog));