import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import {withTranslation} from "react-i18next";
import ManageAccountDialog from "../components/ManageAccountDialog";
import RemoveAccountDialog from "../components/RemoveAccountDialog";
import {Api} from "../network/Api";

const FIRST_DIALOG_CODE = 0;
const SECOND_DIALOG_CODE = 1;

const styles = theme => ({
  root: {
    width: '100%',
  },
});


class ManageAccount extends React.Component {
  state = {
    openedDialog: null,
    userInputText: "",
  };

  handleCloseDialog = () => {
    this.setState({openedDialog: null});
  };

  openFirstDialog = () => {
    this.setState({openedDialog: FIRST_DIALOG_CODE});
  };

  openSecondDialog = () => {
    this.setState({openedDialog: SECOND_DIALOG_CODE});
  };

  ManageAccount = () => {
    Api.removeAccount();
  };

  render() {
    const {classes, t, i18n } = this.props;

    return <div>
      <div style={{textAlign: "center"}}>
        <Button color={"primary"} onClick={this.openFirstDialog}>Manage Account (Advanced)</Button>
      </div>

      <ManageAccountDialog open={this.state.openedDialog===FIRST_DIALOG_CODE} onClose={this.handleCloseDialog} handleRemoveAccountOption={this.openSecondDialog}/>

      <RemoveAccountDialog open={this.state.openedDialog===SECOND_DIALOG_CODE} onClose={this.handleCloseDialog} handleRemoveAccount={this.handleRemoveAccount}/>

    </div>
  }
}

ManageAccount.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(ManageAccount));