const styles = theme => ({
  root: {
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },

  balance: {
    color: "#009900",
    fontWeight: "bold",
    textAlign: 'center',
    fontSize: 'large'
  },

  lifetimeBalance: {
    color: "#009900",
    textAlign: 'center',
  },

  button: {
    display: 'flex',
    margin: 'auto',
    //border: '1px solid blue',
    //color: 'blue',
    alignItems: 'center',
    width: '100%',
  },
  success: {
    backgroundColor: green[600],
  },
  error: {
    backgroundColor: theme.palette.error.dark,
  },
  info: {
    backgroundColor: theme.palette.primary.main,
  },
  warning: {
    backgroundColor: amber[700],
  },
  icon: {
    fontSize: 20,
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing.unit * 1,
  },
  message: {
    display: 'flex',
    alignItems: 'center',
  },
});

class AffiliatesDialog extends React.Component {

  state = {
    open: false
  };


  componentDidMount() {

  }

  handleChange = event => {
    this.setState({
      minutes: parseInt(event.target.value),
    });
  };

  withdrawFunds = (balance, paypal) => {
    Api.withdrawFunds(balance, paypal, ()=>{this.setState({open: true})}, null);
  };


  onLearnMore = () => {
    window.document.getElementById("affiliatesInfoLink").click();
  };




  render() {
    const {classes, t, i18n, userRealtimeData, onSwitchPaypal } = this.props;

    let referrerId = userRealtimeData ? userRealtimeData.referrerId : null;
    let paypal = userRealtimeData ? userRealtimeData.affiliatePaypal : null;
    let balance = userRealtimeData ? userRealtimeData.earnedBalance : 0;
    let lifeEarned = userRealtimeData ? userRealtimeData.lifeEarned : 0;
    let mayRedraw = balance >= 50 && paypal!=null;

    if (this.props.open) {
      return <Dialog
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
        className={classes.root}
      >
        <DialogTitle id="scroll-dialog-title">{'Affiliates program - for a lifetime income'}</DialogTitle>
        <DialogContent>

          <Typography variant="subtitle1" align="justify" paragraph className={classes.balance}>
            {'Earnings balance: $' + balance}
          </Typography>
          <Typography variant="subtitle1" align="justify" paragraph className={classes.lifetimeBalance}>
            {'Lifetime earnings: $' + lifeEarned}
          </Typography>

          <Typography variant="subtitle1" align="justify" paragraph>
            {'Earn 10% for lifetime out of every purchase anyone you referred WILL EVER place. Withdraw earnings once your available earnings reach $50 or more.'}
            &nbsp;<Link href={'https://speechnotes.co/files/affiliates'} target={'_blank'}>Learn more</Link>
          </Typography>


          <ShareReferrerLink referrerId={referrerId} isAffiliate={true} />

          {(referrerId==null || referrerId==undefined || referrerId=='null' || referrerId=='undefined' || referrerId.length==0) ?
            <div></div> :
            <div>
              <Typography variant="caption" align="center" paragraph>
                {'Paypal account to transfer earnings to: ' + (paypal ? paypal : 'none set')}
              </Typography>
              <Button variant="outlined" color={'primary'} className={classes.button}
                      onClick={onSwitchPaypal}>{'Set or switch Paypal account'}
              </Button>
              <br/>
              {(paypal==null || paypal==undefined || paypal=='null' || paypal=='undefined' || paypal.length==0) ? <div/> :
                (!mayRedraw ? <Typography variant="caption" align="center" paragraph>
                  {'Withdrawing will become available once you reach available earnings of $50 or more.'}
                </Typography> :
                  <Button variant="outlined" color={'primary'} className={classes.button} disabled={!mayRedraw}
                          onClick={() => this.withdrawFunds(balance, paypal)}>{'Request to withdraw earnings'}
                  </Button>)
              }
            </div>
          }

          <br/><br/>
          <Typography variant="caption" align="center" paragraph>
            {'Any further questions? Contact us at admin@speechlogger.com'}
          </Typography>

        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="secondary">
            {'close'}
          </Button>

        </DialogActions>

        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={120000}
          onClose={() => this.setState({open: false})}
        >
          <SnackbarContent
            className={classes.success}
            aria-describedby="client-snackbar"
            message={
              <span id="client-snackbar" className={classes.message}>
          <CheckIcon className={classes.icon} />
                &nbsp;
                {'Sent request. Processing time may take a few days.'}
        </span>
            }
            action={[
              <IconButton key="close" aria-label="Close" color="inherit" onClick={() => this.setState({open: false})}>
                <CloseIcon className={classes.icon} />
              </IconButton>,
            ]}
          />
        </Snackbar>
      </Dialog>
    }

    return <div/>
  }
}

AffiliatesDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(AffiliatesDialog));