import {connect} from 'react-redux'
import React from "react";

// This function takes a UI component...
export function withUploadState(WrappedComponent) {

  const mapStateToProps = (state, props) => ({
    uploadState: state.uploadState,
    ...props,
  });

  const mapDispatchToProps = dispatch => ({
    //fireUploadState: uploadState => dispatch(setUploadAction(uploadState))
  });

  // ...and returns another component...
  return connect(
    mapStateToProps,
    mapDispatchToProps
  )(WrappedComponent);
}
