class FileTile extends React.Component {
  state = {
    fileMap: null, // The file to be displayed
  };

  handleTap = event => {
    //this.setState({ mobileMoreAnchorEl: event.currentTarget });
  };

  unsubscribe = () => {};

  componentDidMount() {
    // TODO: Pass fileMap from the FilesGrid, no need to listen - unless - status is not yet final!
    //   TODO: Only if status is not done, and not error => then set up a listener.
    //

    let thisComponent = this;
    let firebaseRef;
    if (this.props.docId == "new") {
      //firebaseRef = MyFirebase.getDbSamplesRef().collection("jobs").doc(this.props.docId);
      this.setState({fileMap: {id: "new"}});
      this.unsubscribe = function () {};
    } else if (this.props.docId == "sample1") {
      //firebaseRef = MyFirebase.getDbSamplesRef().collection("jobs").doc(this.props.docId);
      this.setState({fileMap: Samples.demo3});
      this.unsubscribe = function () {};
    } else {
      firebaseRef = MyFirebase.getDbUserRef().collection("jobs").doc("" + this.props.docId)
      this.unsubscribe = firebaseRef.onSnapshot(function(doc) {
        if (doc.data()!=null && doc.data()!=undefined) {
          let fileMap = doc.data();
          fileMap.id = doc.id;
          thisComponent.setState({fileMap: fileMap});
        }
      });
    }

  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    const {classes, t, i18n } = this.props;


    let fileMap = this.state.fileMap;
    let docId = this.props.docId;

    if (fileMap==null) {
      return <Card/>
    }

    //console.log("rendering tile ", docId, fileMap);

    if (docId=="new") {
      return <Card onClick={() => { {this.props.openDoc(docId, "new", {});} }} className={classes.card} style={{cursor: "pointer"}} >
        <CardContent className={classes.cardContent} style={{display: 'flex', justifyContent: 'center', cursor: "pointer"}}>
        <div style={{textAlign: "center", maxWidth: "80%", margin: "auto", padding: "30px 10px"}}>
          <Typography color={"primary"} style={{fontSize: "large"}}>Transcribe New</Typography></div>
        </CardContent>
      </Card>
    }

    if (docId=="sample1") {
      return <Card className={classes.card}>
        <CardContent className={classes.cardContent}>
          <Typography color={"primary"} style={{cursor: "pointer"}} onClick={() => { {this.props.openDoc(docId, fileMap.fileName, fileMap);} }} gutterBottom variant="h5" component="h5">
            {fileMap.fileName}
          </Typography>
          <Typography variant="body1" paragraph>
            <span style={{color: "grey"}}>{(new Date()).toString().replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/,'$2-$1-$3')}</span>
            &nbsp;
            {fileMap.fileUrl && <Link style={{}} href={fileMap.fileUrl} target={"_blank"}>
              <PlayCircleOutlineIcon />
            </Link>}
          </Typography>
          {
            <Typography>
              {/*Real transcription results as received for this video <Link href={"https://youtu.be/aCs21PsNjNA"} target="_blank">https://youtu.be/aCs21PsNjNA</Link>*/}
              {fileMap.preview}
            </Typography>
          }
        </CardContent>
        {this.getCardActions(docId, fileMap, t, classes)}
      </Card>

    }

    if (fileMap!=null) {

      const errorPopover = () => {
        if ((fileMap.status == null || fileMap.status == undefined || fileMap.status.length == 0)) {
          return null;
        } else if (fileMap.status == "DONE_STATUS") {
          return null;
        }
        let errorText = fileMap.status;
        return true;
      };

      return <Card className={classes.card}>
        <CardContent className={classes.cardContent}>
          <Typography color={(fileMap.status=="DONE_STATUS") ? "primary" : "textPrimary"} style={{cursor: "pointer"}} onClick={() => { if (fileMap.status=="DONE_STATUS") {this.props.openDoc(docId, fileMap.fileName, fileMap);} }} gutterBottom variant="h5" component="h5">
            {fileMap.fileName}
          </Typography>
          <Typography variant="body1" paragraph>
            <span style={{color: "grey"}}>{fileMap.orderedAt.toDate().toLocaleString()}</span>
            &nbsp;
            {fileMap.fileUrl && <Link style={{}} href={fileMap.fileUrl} target={"_blank"}>
              <PlayCircleOutlineIcon />
            </Link>}
          </Typography>
          {fileMap.status=="DONE_STATUS" ?
            <Typography>
              {fileMap.preview}
            </Typography>
            :
            ((errorPopover==null || ErrorCodes.errorToExplanation(fileMap.status)==null || fileMap.exitCode!==1) ?
              <div>
                <Typography>
                  <span style={{color: "#98f", textAlign: "center"}}>{"In progress. " + fileMap.status}</span>
                </Typography>
              </div>
                :
                <div>
                  <Typography>
                    <span style={{color: "#f00", textAlign: "center"}}>{"" + fileMap.status}</span>
                  </Typography>
                  <div style={{color: "gray"}}>
                    {ErrorCodes.errorToExplanation(fileMap.status)}
                  </div>
                </div>
            )
          }
          <Tooltip title="Delete forever" aria-label="Delete forever">
            <div className={classes.deleteButton + " deleteButton"} onClick={() => { this.props.removeDoc(docId, fileMap.fileName, fileMap); }} >
              <DeleteIcon />
            </div>
          </Tooltip>
          <Tooltip title="Copy whole transcript to clipboard" aria-label="Copy whole transcript to clipboard">
            <div className={classes.copyButton + " copyButton"} onClick={() => { this.props.downloadFile(docId, fileMap.fileName, "copy", fileMap); }} disabled={fileMap.status!="DONE_STATUS"}>
              <CopyIcon />
            </div>
          </Tooltip>
        </CardContent>
        {this.getCardActions(docId, fileMap, t, classes)}
      </Card>
    }

    return <Card/>
  }

  getCardActions(docId, fileMap, t, classes) {

    let handleAction = (type) => {
      let enabled = fileMap.status==="DONE_STATUS" || fileMap.exitCode===0;

      if (!enabled) {
        if (fileMap.exitCode===-1) {
          alert('Document is still processing. Processing time is usually half the audio duration. We will send you an email when ready.');
        } else if (fileMap.exitCode===1) {
          alert('Transcription job failed: ' + fileMap.status);
        } else {
          alert('Document not available. Either still in progress, or attempt failed.');
        }
        return;
      }

      if (type=="view") {
        this.props.openDoc(docId, fileMap.fileName, fileMap);
      } else {
        this.props.downloadFile(docId, fileMap.fileName, type, fileMap);
      }
    }

    return <CardActions style={{direction: "rtl", width: "100%", overflow: "hidden", backgroundColor: "#fcc169", padding: "3px 0" }}>
      <Tooltip title={t('view')} aria-label="Open to view, edit & download">
        <div className={classes.actionButton} size="small" color="primary" onClick={() => { handleAction("view"); } }>
          <LaunchIcon/>
        </div>
      </Tooltip>
      <Tooltip title="Download as txt file" aria-label="Download as txt file">
        <div className={classes.actionButton} style={{fontWeight: "bold", fontSize: "larger"}} size="small" color="primary" onClick={() => { handleAction("txt"); }} disabled={fileMap.status!="DONE_STATUS"}>
          TXT
        </div>
      </Tooltip>
      <Tooltip title="Print / pdf format" aria-label="Print / pdf format">
        <div className={classes.actionButton} color="primary" onClick={() => { handleAction("pdf"); }} disabled={fileMap.status!="DONE_STATUS"}>
          <PdfIcon/><PrintIcon/>
        </div>
      </Tooltip>
      <Tooltip title="Download srt (captions) file" aria-label="Download srt (captions) file">
        <div className={classes.actionButton} style={{fontWeight: "bold", fontSize: "larger"}} size="small" color="primary" onClick={() => { handleAction("srt"); }} disabled={fileMap.status!="DONE_STATUS"}>
          SRT
        </div>
      </Tooltip>
      {/*<div className={classes.actionButton} size="small" color="primary" onClick={() => { handleAction("copy"); }} disabled={fileMap.status!="DONE_STATUS"}>
        <CopyIcon/>
      </div>*/}
      {fileMap.isOnRemoteDb && <Tooltip title="Send transcript to my email" aria-label="Send transcript to my email">
        <div className={classes.actionButton} size="small" color="primary" onClick={() => { handleAction("email"); }} disabled={fileMap.status!="DONE_STATUS" || !fileMap.isOnRemoteDb}>
          <EmailIcon/>
        </div>
      </Tooltip>}
    </CardActions>;
  }
}

FileTile.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(FileTile));