
export default class GenericUtils {

  // Returns one of "android", "ios", "mac", "win", "unknown".
  static getOs() {
    if (!window.navigator) {
      return "unknown";
    }

    let userAgentLowerCaseString = ("" + navigator.userAgent + " " + navigator.platform + " " + navigator.userAgentData).toLowerCase();
    if (/android/i.test(userAgentLowerCaseString)) {
      return "android";
    }

    if (/mac/i.test(userAgentLowerCaseString) && "ontouchend" in document) {
      return "ios";
    }

    if (/ipad|iphone|ipod|ios/i.test(userAgentLowerCaseString)) {
      return "ios";
    }

    if (/mac/i.test(userAgentLowerCaseString)) {
      return "mac";
    }

    if (/win/i.test(userAgentLowerCaseString)) {
      return "win";
    }

    return "unknown"
  }

  static generateRandomString = n => {
    let allChars = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
    let str = "";
    for (let i=0; i<n; i++) {
      let N = allChars.length-1;
      let randomPos = Math.floor(N * Math.random());
      let randomChar = allChars.charAt(randomPos);
      str += randomChar;
    }
    return str;
  }

  static millisecondsToReadableTimestamp = timeMilli => {
    let srtFormattedTime= '' +
      (Math.floor((timeMilli)/60000)) + ':' +
      GenericUtils.addZero(Math.floor((timeMilli%60000)/1000))/* + ',' +
      GenericUtils.addDoubleZero(Math.floor(timeMilli%1000))*/;
    return srtFormattedTime;
  };

  static shouldInsertTimeStamp = (rateInMinutes, t1seconds, t2seconds) => {
    // if t1 < rateInMinutes * n && t2 > rateInMinutes * n
    for (let n=0 ; n < 600; n ++) {
      let borderTimeInSecs = rateInMinutes * n * 60;
      if (t1seconds < borderTimeInSecs && t2seconds > borderTimeInSecs) {
        return true;
      }

      if (t1seconds < borderTimeInSecs && t2seconds < borderTimeInSecs) {
        return false;
      }
    }

    return false;
  }

  static addZero = (i) => {
    if (i<10) { i="0" + i; }
    return i;
  };

  static addDoubleZero = (i) => {
    if (i<100) {
      if (i<10) {
        i="00" + i;
      } else {
        i="0" + i;
      }
    }
    return i;
  };
}