import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import ExpansionPanelSummary from "@material-ui/core/AccordionSummary";
import ExpansionPanel from "@material-ui/core/Accordion";
import Typography from "@material-ui/core/Typography";
import ExpansionPanelDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Link from "@material-ui/core/Link";
import Languages from "../utils/Languages";
import {withTranslation} from "react-i18next";
import Consts from "../utils/Consts";


const styles = theme => ({
  root: {
    width: '100%',
  },
  heading: {
    whiteSpace: 'pre-line',
    color: "#537dca",

    "&:hover": {
      background: "aliceblue"
    },
  },
  content: {
    fontWeight: "100",
    fontSize: theme.typography.pxToRem(18),
    color: "#444",
    whiteSpace: 'pre-line'
  },
});


class AboutExpansion extends React.Component {
  state = {
    expanded: null,
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };


  createContent = () => {
    const {classes, t, i18n } = this.props;
    const { expanded } = this.state;

    let children = [];

    // Outer loop to create parent
    for (let i = 1; i <= 14; i++) {
      let panelId = 'panel' + i;
      children.push(
        <ExpansionPanel expanded={expanded === panelId} onChange={this.handleChange(panelId)} key={panelId}>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />} className={classes.heading}>
            <h5>{t('about.' + i + '.title')}</h5>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Typography className={classes.content}>
              {(i==8) ? Consts.SUPPORTED_FILES + "\n\n" : ""}
              {t('about.' + i + '.desc')}
              <span className="Flag-font">{(i==7) ? Languages.supportedLanguagesString() : ""}</span>
              {t('about.' + i + '.link.url').length>0 ? <Link rel="nofollow" target={"_blank"} href={t('about.' + i + '.link.url')} className={classes.link}>
                 {"\n\n" + t('about.' + i + '.link.label')}
              </Link> : "" }
            </Typography>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      );
    }

    return children;
  };

  render() {
    const {classes, t, i18n } = this.props;
    const { expanded } = this.state;

    return (
      <div className={classes.root}>
        {this.createContent()}
      </div>
    );
  }
}

AboutExpansion.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(AboutExpansion));