import {ACTION_TYPES} from "../actions";

export const prefTimesReducer = (state = true, action) => {
  switch (action.type) {
    case ACTION_TYPES.userPrefPrintTimes:
      return action.yesNo;
    default:
      return state
  }
};

export const prefSpeakersReducer = (state = true, action) => {
  switch (action.type) {
    case ACTION_TYPES.userPrefPrintSpeakers:
      return action.yesNo;
    default:
      return state
  }
};


