import React, {Component} from "react";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import Helpers from "../utils/Helpers";
import DialogContentText from "@material-ui/core/DialogContentText";
import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import DeleteIcon from "@material-ui/icons/Delete";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";
import PropTypes from "prop-types";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import Languages from "../utils/Languages";
import PdfIcon from "@material-ui/icons/PictureAsPdf";
import PrintIcon from "@material-ui/icons/Print";
import DownloadIcon from "@material-ui/icons/SaveAlt";
import EditIcon from "@material-ui/icons/Create";
import SaveIcon from "@material-ui/icons/Save";
import BackIcon from "@material-ui/icons/ArrowBackIos";
import CopyIcon from "@material-ui/icons/FileCopy";
import UndoIcon from "@material-ui/icons/Undo";
import RedoIcon from "@material-ui/icons/Redo";
import ContentEditable from 'react-contenteditable'
import {Api} from "../network/Api";
import ReactAudioPlayer from 'react-audio-player';
import Consts from "../utils/Consts";
import {withPrefs} from "../redux/containers/withPrefs";
import PrintPrefsRow from "./PrintPrefsRow";
import Typography from "@material-ui/core/Typography";

const styles = theme => ({
  actionButton: {
    textTransform: "uppercase",
    color: "#3f51b5",
    backgroundColor: "white",
    cursor: "pointer",
    display: "inline-block",
    borderRadius: "5px",
    padding: "6px 6px",
    marginLeft: "8px",
    whiteSpace: "nowrap",
    '&:hover': {
      color: "blue",
    },
  },
  gridButton: {
    padding: "5px",
    margin: "8px",
    cursor: "pointer",
    color: "#3f51b5",
    '&:hover': {
      color: "blue",
    }
  },
  transcriptSectionHeader: {
    display: 'inline-block',
    whiteSpace: "pre",
    fontSize: "small",
    color: "gray"
  },
  wordSpan: {
    display: 'inline',
    whiteSpace: "pre-wrap",
    fontSize: "large",
    position: "relative"
  },

  root: {
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  hidden: {
    display: "none",
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
  urlInput: {
    border: 'solid 1px gray',
  },
  actionsContainer: {
    textAlign: "right",
  },
  selectFileButton: {
    cursor: "pointer",
    color: theme.palette.primary.main
  }
});


class DocOpenDialog extends Component {

  state = {
    isToShowAlert: false,
    alertCounter: 0
    // srcUrl: "https://ttsreader.com/actors/1005/sample2.mp3"  // TODO: when we add audio player will be relevant
  }

  componentDidMount() {
    this.setState({isExport: this.props.isExport});
  }

  componentWillReceiveProps(nextProps, nextContext) {
    let isLoading = Boolean(nextProps.captions==-1);
    let isExport = false;
    this.setState({captions: false, docData: nextProps.docData, isLoading: isLoading, isExport: isExport});
    if (this.props.openedDocId!=nextProps.openedDocId) {
      this.setState({localFileBlob: null});
    } else {

    }
  }

  handleBack = () => {
    if (this.state.isExport) {
      this.setState({isExport: false, isPopupSaveOrDiscard: false});
    } else {
      this.props.handleDocClose();
    }
  }

  handleEditStart = () => {
    //console.log('edit start');
    let newCounter = this.state.alertCounter+1;
    let isToShowAlert = false;
    let preEditCaptions = this.state.captions || this.props.captions;

    if (newCounter==1) {
      isToShowAlert = true;
      preEditCaptions = null;
    }

    this.setState({preEditCaptions: preEditCaptions, isEdit: !isToShowAlert, isExport: false, isToShowAlert: isToShowAlert, alertCounter: newCounter});
  }

  handleEditSaveAndDone = () => {
    this.handleEditSave();
    this.setState({isEdit: false, isExport: false, isPopupSaveOrDiscard: false});
  }

  handleEditDone = () => {
    let captions = Helpers.elementToCaptions(document.getElementById("captionsContainer"));
    this.setState({isPopupSaveOrDiscard: true, captions: captions});
  }

  handleEditUndo = () => {
    document.execCommand('undo');
  }

  handleEditRedo = () => {
    document.execCommand('redo');
  }

  handleEditDiscard = () => {
    let captions = this.state.preEditCaptions || this.state.captions;
    this.setState({captions: captions, isEdit: false, isExport: false, isPopupSaveOrDiscard: false});
  }

  handleEditSave = () => {
    let captions = Helpers.elementToCaptions(document.getElementById("captionsContainer"));
    //console.log(captions);
    this.setState({captions: captions, preEditCaptions: null});
    Api.updateTranscriptWithEditedCaptions(this.props.openedDocId, captions, this.state.docData);
  }

  handleFileChange = (e) => {
    const files = e.currentTarget.files;
    if (files == null || files.length==0) {
      return;
    }

    let file = files[0];
    let self = this;

    let fileURL = URL.createObjectURL(file);
    self.setState({ localFileBlob: fileURL });

    /*let reader = new FileReader();
    reader.onload = function (e) {
      self.setState({ localFileBlob: e.target.result });
    }

    reader.readAsDataURL(file);*/
  };

  handleAudioEvent = (eventName, ev) => {
    console.log('audio event: ', eventName, ", ", ev);
    let atSecond = -1;
    if (eventName=='onSeeked') {
      atSecond = ev.target.currentTime;
    } else if (eventName == 'onListen') {
      atSecond = ev;
    }

    if (atSecond>=0) {
      this.jumpToSecond(atSecond);
    }
  }

  lastEl = null;
  jumpToSecond = (sec) => {
    // get captions.
    // find with binary search the time it should have
    // find element with the found time attribute.
    // el.scrollToView()
    let captions = this.state.captions || this.props.captions;
    let captionFit = Helpers.findCaptionForSec(captions, sec);
    let timeStartFit = captionFit[1];
    let selector = `[data-s=\"${timeStartFit}\"]`;
    console.log('looking for ', selector, timeStartFit, captionFit);
    let el = document.querySelector(selector);
    if (el && el!=this.lastEl) {
      if (this.lastEl) {
        this.lastEl.classList.remove("highlighted");
      }


      el.classList.add("highlighted");
      el.scrollIntoView({ behavior: 'smooth', block: 'center' });
      this.lastEl = el;
    }
  }

  componentDidCatch(error, errorInfo) {
    // You can also log the error to an error reporting service
    console.log('caught ronen: ', error, errorInfo);
  }

  render() {
    const {classes, t, i18n } = this.props;
    let captions = this.state.captions || this.props.captions;

    const PrefsExportRow = withPrefs(PrintPrefsRow);

    return <Dialog
      open={this.props.open}
      onClose={this.state.isEdit ? this.handleEditDone : this.props.handleDocClose}
      aria-labelledby="dialog-title"
    >
      <DialogTitle>{this.props.docTitle}</DialogTitle>

      <DialogContent style={{position: "relative", overflowY: "hidden", }}>

        { (this.state.docData && (Helpers.isPotentiallyDirectMediaLink(this.state.docData.fileUrl) || this.state.localFileBlob)) ? <ReactAudioPlayer
          controls={true}
          src={Helpers.isPotentiallyDirectMediaLink(this.state.docData.fileUrl) ? this.state.docData.fileUrl : this.state.localFileBlob}
          ref={(element) => { this.audioElement = element; }}
          onLoadedMetadata  = {(e)=>this.handleAudioEvent('onLoadMetadata',e)}
          onSeeked          = {(e)=>this.handleAudioEvent('onSeeked',e)}
          onPlay            = {(e)=>this.handleAudioEvent('onPlay',e)}
          onPause  = {(e)=>this.handleAudioEvent('onPause',e)}
          onListen  = {(e)=>this.handleAudioEvent('onListen',e)}
          onEnded  = {(e)=>this.handleAudioEvent('onEnded',e)}
          onCanPlay  = {(e)=>this.handleAudioEvent('onCanPlay',e)}
          listenInterval  = {400}
          style={{marginBottom: "20px", outline: "none"}}
        /> : (this.state.docData && Helpers.isYouTubeUrl(this.state.docData.fileUrl)) ? <div></div> :
          <div style={{maxWidth: "80%", margin: "auto", marginBottom: "20px"}}>
          <input type="file" ref={ref => this.fileInputRef = ref} multiple={false} className={classes.hidden} name="file" id="file" onChange={ (e) => this.handleFileChange(e) } accept={Consts.SUPPORTED_FILES}/>
          <label style={{display: "block", margin: "auto", border: "2px solid #3f51b5",padding: "2px 10px", fontWeight: "bold", textAlign: "center", borderRadius: "10px" }} htmlFor="file" className={classes.selectFileButton}>Select Source File to Enable Sync-Play</label>

            <p style={{color: "gray", marginTop: "10px"}}>Has to be re-selected since we do not store the source audio files. Will play locally without upload.</p>
        </div> }

        <div style={{overflowY: "scroll", maxHeight: (window.innerHeight-250)+"px"}}>

          <style dangerouslySetInnerHTML={{__html: `
            ${ (!((navigator.userAgent.toLowerCase().indexOf('chrome') > -1) &&(navigator.vendor.toLowerCase().indexOf("google") > -1)) || this.state.isEdit) ? "" : ".wordSpan::before {" +
              "content: attr(data-timestamp);" +
              "color: yellow;" + 
              "display: block;" +
              "    position: absolute;" +
              "    top: 0;" +
              "    margin-left: -90px;" +
              "    backgroundColor: white;" +
              "    color: darkgray;" +
              "    z-index: 99;" +
              "    outline: none;" +
              "background-color: white;" + 
              "  " + 
              "}" 
            }
            .wordSpan:hover::after { 
              content: attr(data-timestamp);
              position: absolute;
              left: -70px;
              top: -10px;
              background-color: black;
              color: white;
              border-radius: 15px;
              padding: 2px 15px;
              z-index: 999;
            }
            .wordSpan:hover {
              background-color: yellow;
              caret-color: black;
            }
            .highlighted {
              border: blue 1px solid
            }
          `}}></style>

          {this.props.openedDocId == "sample1" &&
          <div><p style={{fontSize: "large", color: "gray"}}><i>
            Real, untouched transcription results. Speaker has thick accent. Watch the video here: <a target={"_blank"} href={this.state.docData.fileUrl}>{this.state.docData.fileUrl}</a>
          </i></p>
            <hr/><br/></div>
          }

          {(this.state.docData && Helpers.isYouTubeUrl(this.state.docData.fileUrl)) && <div>
            <iframe style={{height: "200px", maxWidth: "100%", marginBottom: "20px" }} width="375" height="200" src={"https://www.youtube-nocookie.com/embed/" + Helpers.getYouTubeIdFromUrl(this.state.docData.fileUrl)} frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen></iframe>
          </div>}

          {(!this.state.isLoading && this.props.captions) ?
            <div>
              <ContentEditable id="captionsContainer" style={{marginLeft: (/Chrome/.test(navigator.userAgent)) ? "100px" : "10px", padding: "5px", border: this.state.isEdit ? "solid blue 1px" : "none" }}
                               disabled={!this.state.isEdit}
                               tagName = 'p'
                               html={Helpers.captionsToInnerHtml(captions, classes, this.state.isEdit)}
              />
            </div> :
            this.props.docText ?
              <DialogContentText style={{whiteSpace: 'pre-line'}}>
                {this.props.docText}
              </DialogContentText> :
              <div style={{textAlign:"center"}}>
                <CircularProgress className={classes.progress} />
              </div>
          }
        </div>

        {this.state.isExport && <div style={{position: "absolute", color: "#3f51b5", bottom: "0", top: "0", right: "0", left: "0", zIndex: "999", backgroundColor: "#00000022", pointerEvents: "all"}}>
          <div style={{position: "absolute", textAlign: "center", bottom: "0", right: "0", left: "0", backgroundColor: "white", padding: "20px"}}>
            <div>
              <p style={{fontSize: "large", fontWeight: "bold"}}>Export / Download</p>
              <div style={{position: "absolute", top: "20px", right: "20px"}} onClick={()=>{this.setState({isExport: false})}}><Button>X</Button></div>
            </div>
            <div style={{}}>
              <Grid container spacing={3} style={{textAlign: "center"}} >
                <Grid item xs>
                  <div className={classes.gridButton} onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "txt" )}}><b>TXT</b><br/><br/>Text File</div>
                </Grid>
                <Grid item xs>
                  <div style={{textAlign: "center"}} className={classes.gridButton} onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "copy" )}}><CopyIcon/><br/><br/>Copy to Clipboard</div>
                </Grid>
                <Grid item xs>
                  <div style={{textAlign: "center"}} className={classes.gridButton} onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "pdf" )}}><PdfIcon/><PrintIcon/><br/><br/>Print / PDF</div>
                </Grid>
              </Grid>

              <Grid container spacing={3} style={{textAlign: "center"}} >
                <Grid item xs>
                  <div className={classes.gridButton} onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "srt" )}}><b>SRT</b><br/><br/>Captions</div>
                </Grid>
                <Grid item xs>
                  <div style={{textAlign: "center"}} className={classes.gridButton}  onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "docx" )}}><b>DOCX</b><br/><br/>Word</div>
                </Grid>
                <Grid item xs>
                  <div style={{textAlign: "center"}} className={classes.gridButton}  onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "other" )}}><b>+</b><br/><br/>Other</div>
                </Grid>
              </Grid>
            </div>
          </div>

        </div>}

        {this.state.isEdit && this.state.isPopupSaveOrDiscard && <div style={{position: "absolute", color: "#3f51b5", bottom: "0", top: "0", right: "0", left: "0", zIndex: "999", backgroundColor: "#00000022", pointerEvents: "all"}}>
          <div style={{position: "absolute", textAlign: "center", bottom: "0", right: "0", left: "0", backgroundColor: "white", padding: "20px"}}>
            <div>
              <p style={{fontSize: "large", fontWeight: "bold"}}>Save Changes?</p>
              <div style={{position: "absolute", top: "20px", right: "20px"}} onClick={()=>{this.setState({isPopupSaveOrDiscard: false})}}><Button>X</Button></div>
            </div>
            <div style={{}}>
              <br/>
              <div style={{textTransform: "uppercase", padding: "5px", fontSize: "larger", width: "80%", margin: "auto", textAlign: "center", borderRadius: "20px", backgroundColor: "#3f51b5", color: "white", border: "1px solid #3f51b5", cursor: "pointer"}} onClick={this.handleEditSaveAndDone}><SaveIcon/>&nbsp;{t('save')}</div>
              <br/>
              <div style={{textTransform: "uppercase", padding: "5px", fontSize: "larger", width: "80%", margin: "auto", textAlign: "center", borderRadius: "20px", backgroundColor: "white", color: "#3f51b5", border: "1px solid #3f51b5", cursor: "pointer"}} onClick={this.handleEditDiscard}>{t('discard')}</div>
              <br/>
            </div>
          </div>

        </div>}

        {this.state.isToShowAlert && this.state.alertCounter==1 && this.AlertDismissible(true, 'Recommendation to Backup (Export) First','It is a good idea to download a backup copy prior to editing. Specifically, editing might confuse timestamps, so if you need the subtitles format (.srt) - please download it prior to editing.')}
      </DialogContent>

      <DialogActions>
        {this.state.isExport && <PrefsExportRow/>}
        {!this.state.isEdit && <div className={classes.actionButton} onClick={this.handleBack} color="primary">
          <BackIcon />
        </div>}
        {/*!this.state.isExport && !this.state.isEdit && <div className={classes.actionButton} onClick={()=>{Helpers.exportCaptions(captions, this.props.docTitle, "copy" )}} color="primary">
          <CopyIcon />
        </div>*/}
        {!this.state.isExport && !this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.handleEditStart(); }} color="primary">
          <EditIcon />&nbsp;{t('edit')}
        </div>}
        {!this.state.isExport && !this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.setState({isExport: true}) }} color="primary">
          <DownloadIcon />&nbsp;{t('export')}
        </div>}
        {this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.handleEditUndo(); }} color="primary">
          <UndoIcon/>
        </div>}
        {this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.handleEditRedo(); }} color="primary">
          <RedoIcon/>
        </div>}
        {this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.handleEditSave(); }} color="primary">
          <SaveIcon />
        </div>}
        {this.state.isEdit && <div className={classes.actionButton} onClick={() => { this.handleEditDone(); }} color="primary">
          {t('done')}
        </div>}
      </DialogActions>
    </Dialog>;
  }

  AlertDismissible = (show, title, message) => {
    console.log('got to here: AlertDismissible', show, title, message);

    return (

      <div className={"ronenPopup"} style={{textAlign: "center", padding: "20px", backgroundColor: "#bdf2bf", position: "fixed", width: "80%", left: "10%", bottom: "80px", zIndex: 999}}>
        <Typography variant={"h6"}>{title}</Typography>
        <Typography variant={"body1"}>{message}</Typography>
        <br/>
        <div>
          <Button onClick={() => {this.setState({isToShowAlert: false});}} variant="outlined" style={{width: "100%", textAlign: "right"}}>
            OK
          </Button>
        </div>
      </div>);
  }

}


DocOpenDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(DocOpenDialog));