const styles = theme => ({
  root: {
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  linkPointer: {
    cursor: 'pointer',
  }
});

class ReferDialog extends React.Component {

  state = {
  };

  render() {
    const {classes, t, i18n, userRealtimeData, openAffiliates } = this.props;

    if (this.props.open) {
      return <Dialog
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
        className={classes.root}
      >
        <DialogTitle id="scroll-dialog-title">{t('new.refer.title')}</DialogTitle>
        <DialogContent>

          <Typography variant="subtitle1" align="justify" paragraph>
            {t('new.refer.description')}
          </Typography>

          <ShareReferrerLink referrerId={userRealtimeData ? userRealtimeData.referrerId : null} />

          <br/>
          <br/>
          <Typography variant="subtitle1" align="center" paragraph>
            {'Looking for more?'}&nbsp;<Link className={classes.linkPointer} onClick={openAffiliates} style={{color: "#3f51b5", textDecoration: "underline"}}>Go to the affiliates program</Link>
          </Typography>
          {/*<Button onClick={openAffiliates}>
            Open the affiliates program
          </Button>*/}

        </DialogContent>
        <DialogActions>
          <Button className={classes.button} onClick={this.props.onClose} color="secondary">
            {t('cancel')}
          </Button>

        </DialogActions>
      </Dialog>
    }

    return <div/>
  }
}

ReferDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(ReferDialog));