import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {Api} from "../../network/Api";

class ThankYou extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: '',
      gender: '',
      age: '',
    };
  }

  componentWillMount() {
    const { steps } = this.props;
    const { main, input_feedback, input_issue, input_language, input_file_type, input_youtube } = steps;
    let asString = "";

    asString += main ? `main=${main.value}&` : "";
    asString += input_feedback ? `input_feedback=${input_feedback.value}&` : "";
    asString += input_issue ? `input_issue=${input_issue.value}&` : "";
    asString += input_language ? `input_language=${input_language.value}&` : "";
    asString += input_file_type ? `input_file_type=${input_file_type.value}&` : "";
    asString += input_youtube ? `input_youtube=${input_youtube.value}&` : "";

    console.log(asString);
    Api.sendChatData(asString);

    this.setState({ input_feedback, input_issue, input_language, input_file_type, input_youtube });
  }

  render() {
    //const { name, gender, age } = this.state;
    return (
      <div>
        <a href={"https://speechnotes.co/files/guide/"} target={"_blank"}>https://speechnotes.co/files/guide/</a>
      </div>
    );
  }
}

ThankYou.propTypes = {
  steps: PropTypes.object,
};

ThankYou.defaultProps = {
  steps: undefined,
};

export default ThankYou;