import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import {withTranslation} from "react-i18next";
import {MyFirebase} from "../network/MyFirebase";
import StepPicker from "./StepPicker";
import OffTheShelfPicker from "./OffTheShelfPicker";


const styles = theme => ({
  root: {
    maxWidth: '95%',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  dialogContent: {
    width: "600px",
    maxWidth: "100%",
    marginRight: '5px',
    overflowY: 'scroll',
    '&::-webkit-scrollbar': {
    },
    '&::-webkit-scrollbar-thumb': {
    },
  },
  caption: {
    textTransform: 'uppercase'
  },
});

class PurchaseDialog extends React.Component {

  state = {
    minutes: 120,
  };

  componentDidMount() {
    console.log('is paypal div on page? ', document.getElementById('paypal-button-container'));
  }

  componentDidUpdate = () => {
    setTimeout(() => {
      console.log('minutes: ' + this.state.minutes);
      let el = document.getElementById('paypal-button-container');
      if (el && window.resetPayPalButton) {
        window.resetPayPalButton(this.state.minutes*0.1, MyFirebase.getUserId(),() => {this.props.onSuccess(); this.props.onClose();});
      }
    },1);
  };

  steps = [45, 120, 600, 1200, 6000];

  handleChange = (i) => {
    console.log('step changed. new step = ' + i);
    if (i>=0 && i<this.steps.length) {
      this.setState({
        minutes: this.steps[i],
      });
    }

  };

  handleBuyClick = (e) => {
    this.buyBtn.click();
  };

  render() {
    const {classes, t, i18n } = this.props;
    const steps = this.steps;
    let defaultStep = 1;
    for (let i=0; i<steps.length; i++) {
      if (steps[i]===this.state.minutes) {
        defaultStep = i;
      }
    }

    let stepsAsText = ["45 " + t('minutes'), "120 " + t('minutes'), "10 " + t('hours'), "20 "+ t('hours'), "100 "+ t('hours')];

    if (this.props.open) {
      return <Dialog
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
        className={classes.root}
      >
        <DialogTitle id="scroll-dialog-title">
          {t('hero.Add credit')}&nbsp;
          { !this.props.didEverPurchase && <span style={{backgroundColor: "lightgreen"}}> Get +10% for free</span>}
        </DialogTitle>
        <DialogContent className={classes.dialogContent}>

          <div style={{textAlign: "center", fontSize: "larger"}}>
            <p>{t('buy.Minutes to add')}</p>
            <p style={{textAlign: "center", color: "gray"}}>{t('buy.only 01 per minute')}</p>
            <OffTheShelfPicker onChange={this.handleChange} steps={stepsAsText} defaultStep={defaultStep} />
            <div style={{fontWeight: "bold", marginTop: "20px", fontSize: "20px"}}>
              {this.state.minutes === 45   && <p> {t('buy.45minutes')}   </p>}
              {this.state.minutes === 120  && <p> {t('buy.120minutes')}  </p>}
              {this.state.minutes === 600  && <p> {t('buy.600minutes')}  </p>}
              {this.state.minutes === 1200 && <p> {t('buy.1200minutes')} </p>}
              {this.state.minutes === 6000 && <p> {t('buy.6000minutes')} </p>}
              { !this.props.didEverPurchase &&
                <p style={{textAlign: "center", color: "green", fontWeight: "normal"}}>
                  🎁 +{this.state.minutes/10} {t('minutes')} free 🎁
                </p>
              }
            </div>
          </div>


            <div id="paypal-button-container" style={{maxWidth:"95%",margin:"30px auto"}}></div>

          <br/>
          <p style={{textTransform: "uppercase", color: "darkblue", textAlign: "center"}}>SECURE PAYMENT</p>

        </DialogContent>
      </Dialog>
    }

    return <div/>
  }
}

PurchaseDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(PurchaseDialog));