import React from 'react';
// First way to import
import {RingLoader} from 'react-spinners';
import Grid from "@material-ui/core/Grid";

export default class Loading extends React.Component {

  render() {

    return (

      <Grid
        container
        spacing={0}
        direction="column"
        alignItems="center"
        justify="center"
        style={{ minHeight: '100vh' }}
      >

        <Grid item>

          <div className='sweet-loading'>
            <RingLoader
              color={'#1233bc'}
              size={90}
              loading={true}
            />
          </div>
        </Grid>

      </Grid>
    );
  }
}