import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux'
import {applyMiddleware, createStore} from 'redux'
import todoAppReducers from './redux/reducers'
import thunk from "redux-thunk";
// import i18n (needs to be bundled ;))
import './i18n';
import GenericUtils from "./utils/GenericUtils";
import JsCssLoader from "./utils/JsCssLoader";

function logger({ getState }) {
  return next => action => {
    console.log('will dispatch', action)

    // Call the next dispatch method in the middleware chain.
    const returnValue = next(action)

    console.log('state after dispatch', getState())

    // This will likely be the action itself, unless
    // a middleware further in chain changed it.
    return returnValue
  }
}

let middleware = [logger, thunk];

const store = createStore(todoAppReducers, {}, applyMiddleware(...middleware));



ReactDOM.render(
  <Provider store={store}>
    <App store={store}/>
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();

function onload() {
  console.log('loaded alright');
  // On windows - load fonts css file for flags:
  let os = GenericUtils.getOs();
  console.log('os: ' + os);

  if (os==="win") {
    const font = new FontFace(
      "BabelStoneFlags",
      "url(static/fonts/BabelStoneFlags.ttf)",
      {
        unicodeRange: "U+1F1E6-1F1FF",
        style: "normal",
        fontSize: "20px",
      }
    );

    document.fonts.add(font);
    font.load().then( () => {
      console.log('loaded emoji font');
      JsCssLoader.fetchStyleSheet('static/css/fonts.css');
    });
  }
}

if (document.readyState === "complete") {
  // Fully loaded!
  console.log('loaded already');
  onload();
} else {
  // Not loaded yet!
  console.log('waiting for load');
  window.addEventListener("load", (event) => {
    onload();
  });
}

