import Consts from "./Consts";
import GenericUtils from "./GenericUtils";
import React from "react";
import ReactDOMServer from 'react-dom/server';
import Samples from "./Samples";
import StoreRepo from "../repos/StoreRepo";


export default class Helpers {

  static wordsToCaptions = words => {
    // [{j: 1, s: 0.2, e: 5.6, t: "hi, my name is Ronen."}
    //  {j: 1, s: 5.6, e: 10.1, t: "How are you my friend?"},
    //  {j:2, s: ... },
    //  ... ]

    if (words.length==0) {
      return [];
    }

    let captions = [];
    let iCaption = {...words[0]};   // Will always get "reset" after pushed.

    if (words.length==1) {
      captions.push(iCaption);
    } else {
      for (let i = 1; i < words.length; i++) {
        let iWord = words[i];
        // We have iCaption populated:
        if (iCaption.j != iWord.j || (iCaption.t + iWord.t).length > 60 || (iWord.s - iCaption.e) > 0.5 || Helpers.isSentenceEnd(iCaption.t)) {
          captions.push({...iCaption});
          iCaption = {...iWord};
        } else {
          iCaption.t += " " + iWord.t;
          iCaption.e = iWord.e;
        }
      }
    }

    return captions;
  }

  static isSentenceEnd(str) {
    let sentenceEnds = [".","!","?",":","。","\n"];
    if (!str) {
      return false;
    }

    while (str[str.length-1]==" ") {
      str = str.slice(0, -1);
    }

    if (sentenceEnds.indexOf(str[str.length-1])>-1) {
      return true;
    }

    return false;
  }

  static jsonToTextWithoutTimes = json => {
    let text = "";
    let transcriptSegments = JSON.parse(json);

    for(let i=0; i<transcriptSegments.length-1; i++) {
      let segment = transcriptSegments[i];
      let segmentNext = transcriptSegments[i+1];
      text += segment.text + " ";
      let timeGap
      if (segment.endTime==null || segment.endTime==undefined) {
        timeGap = 2;
      } else {
        timeGap = segmentNext.time - segment.endTime;
      }
      if (timeGap > 2.5) {
        text += "\n\n";
      } else if (timeGap > 1.5) {
        text += "\n";
      }
    }

    text += transcriptSegments[transcriptSegments.length-1].text + "\n";

    return text;
  };


  static jsonToTextWithTimes = json => {
    let text = "";
    let transcriptSegments = JSON.parse(json);

    for(let i=0; i<transcriptSegments.length; i++) {
      text += "" + Helpers.millisecondsToSrtFormat(transcriptSegments[i].time*1000) + ":\n" + transcriptSegments[i].text + "\n\n";
    }
    return text;
  };

  static jsonToSrt = json => {
    let text = "";
    let transcriptSegments = JSON.parse(json);

    for(let i=0; i<transcriptSegments.length; i++) {
      let durationInMs = 0;
      let maxDurationMs = null;
      if (i<transcriptSegments.length-1) {
        maxDurationMs = (transcriptSegments[i+1].time - transcriptSegments[i].time) * 1000 -1;
      } else {
        maxDurationMs = Helpers.calculateDurationMs(transcriptSegments[i].text, 1000000);
      }

      text += Helpers.transcriptSegmentToSrt(i+1, transcriptSegments[i], maxDurationMs) + "\n";
    }

    return text.trim();
  };

  static captionsToSrt = captions => {
    let text = "";

    for(let i=0; i<captions.length; i++) {
      text += Helpers.captionToSrt(i+1, captions[i]) + "\n";
    }

    return text.trim();
  };


  static calculateDurationMs = (text, maxDurationMs) => {
    let msPerChar = 200; //

    if (text==null || text==undefined || text.length==0 || text.trim().length==0) {
      return 0;
    }

    let calculatedDurationInMs = Math.max(text.trim().length * msPerChar, 1000);
    if (calculatedDurationInMs>maxDurationMs) {
      return maxDurationMs;
    } else {
      return calculatedDurationInMs;
    }
  };


  static millisecondsToSrtFormat = timeMilli => {
    let srtFormattedTime=Helpers.addZero(Math.floor(timeMilli/3600000)) + ':' +
      Helpers.addZero(Math.floor((timeMilli%3600000)/60000)) + ':' +
      Helpers.addZero(Math.floor((timeMilli%60000)/1000)) + ',' +
      Helpers.addDoubleZero(Math.floor(timeMilli%1000));
    return srtFormattedTime;
  };


  static removeEmptyLines = txt => {
    if (txt==null || txt==undefined) {
      return "";
    }

    txt = txt.trim();
    let lines = txt.split("\n");
    let txtWithoutEmpty = "";

    if (lines[0].trim().length>0) {
      txtWithoutEmpty += lines[0].trim();
    }

    for (let i=1; i<lines.length; i++) {
      if (lines[i].trim().length>0) {
        txtWithoutEmpty += "\n" + lines[i].trim();
      }
    }

    return txtWithoutEmpty;

  };


  static addZero = (i) => {
    if (i<10) { i="0" + i; }
    return i;
  };

  static addDoubleZero = (i) => {
    if (i<100) {
      if (i<10) {
        i="00" + i;
      } else {
        i="0" + i;
      }
    }
    return i;
  };




  static transcriptSegmentToSrt = (index, segment, durationMillis) => {
    let startTimeSrtFormatted = Helpers.millisecondsToSrtFormat(segment.time*1000 + 1); // Also adds a ms
    let endTime = segment.endTime ? (segment.endTime*1000) : segment.time*1000 + durationMillis;
    let endTimeSrtFormatted = Helpers.millisecondsToSrtFormat(endTime); // Also reduces a ms
    let captionSrtFormat = Helpers.removeEmptyLines(segment.text);

    let srtSegment = "" + index + "\n" +
      startTimeSrtFormatted + " --> " + endTimeSrtFormatted + "\n" +
      captionSrtFormat + "\n";

    return srtSegment;
  };

  static captionToSrt = (index, caption) => {
    let startTimeSrtFormatted = Helpers.millisecondsToSrtFormat(caption[1]*1000 + 1); // Also adds a ms
    let endTimeSrtFormatted = Helpers.millisecondsToSrtFormat(caption[2]*1000); // Also reduces a ms
    let captionSrtFormat = Helpers.removeEmptyLines(caption[0]);

    let srtSegment = "" + index + "\n" +
      startTimeSrtFormatted + " --> " + endTimeSrtFormatted + "\n" +
      captionSrtFormat + "\n";

    return srtSegment;
  };

  static jsonToDownloadableText = json => {
    let text = "";

    text += "Without timestamps (with-timestamps follows) :\n----------------------\n\n";
    text+=Helpers.jsonToTextWithoutTimes(json);

    text += "\n\nWith timestamps (hh:mm:ss,mmm):\n------------------------------\n\n";
    text+=Helpers.jsonToTextWithTimes(json);

    return text;
  };

  static captionsToDownloadableText = (captions, noTimes, noSpeakerTags) => {
    let text = "";

    let el = document.createElement("p");
    let spans = Helpers.captionsToSpans(captions, false, false, noTimes, noSpeakerTags);
    const htmlString = ReactDOMServer.renderToStaticMarkup(
      <p>
        {spans}
      </p>
    );
    el.innerHTML = htmlString;
    console.log('el = ', el);
    window.ronenEl = el;
    text = el.innerText;

    return text;
  };


  static isReferredBy = () => {
    let referredBy = window.localStorage.getItem("referredBy");
    if (referredBy!=null && referredBy!=undefined && referredBy.length>0 && referredBy!='null' && referredBy!='undefined') {
      return true;
    }

    return false;
  };

  static didEverPurchase = (userDataMap) => {
    if (userDataMap==null || userDataMap==undefined) {
      return false;
    }

    if (userDataMap.dollarBalance!=null && userDataMap.dollarBalance!=undefined && userDataMap.dollarBalance>0) {
      return true;
    }

    if (userDataMap.lifeSpent!=null && userDataMap.lifeSpent!=undefined && userDataMap.lifeSpent>0) {
      return true;
    }

    return false;
  };


  /// Example url:
  ///    https://drive.google.com/file/d/1SmigZxnI6RwUlB1VFzr5QFOGvVcl66X5/view?usp=sharing
  static getDriveIdFromShareUrl = (url) => {
    if (url==null || url==undefined) {
      return "";
    }
    url = url.trim();
    if (url.length==0) {
      return "";
    }

    if (url.startsWith("https://drive.google.com/file/d/")) {
      url = url.substring(32);
      url = url.split("/")[0];
      return url;
    }

    if (url.startsWith("https://drive.google.com/open?id=")) {
      url = url.substring(33);
      url = url.split("/")[0].split("&")[0];
      return url;
    }

    return "";
  };

  /// Example url:
  ///    https://drive.google.com/file/d/1SmigZxnI6RwUlB1VFzr5QFOGvVcl66X5/view?usp=sharing
  /// Should become:
  ///    https://drive.google.com/uc?export=download&id=1SmigZxnI6RwUlB1VFzr5QFOGvVcl66X5
  static getDriveDownloadableFromLink = (url) => {
    if (url==null || url==undefined) {
      return "";
    }

    if (url.startsWith("https://drive.google.com/uc?export=download")) {
      return url;
    }

    let id = Helpers.getDriveIdFromShareUrl(url);

    if (id.length==0) {
      return "";
    }

    return "https://drive.google.com/uc?export=download&id=" + id + "";
  }

  static actuallyDownloadTextAsFile = (txt, fileName) => {
    const element = document.createElement("a");
    const file = new Blob([txt], {type: 'text/csv'});
    element.href = URL.createObjectURL(file);
    element.setAttribute('download', fileName);
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
    element.parentNode.removeChild(element);
  }

  static hasMultipleSpeakers(words) {
    let speaker0;
    for (const word of words) {
      if (!speaker0 && word.j == 0) {
        return false;
      }

      if (speaker0) {
        if (word.j) {
          if (speaker0!=word.j) {
            return true;
          }
        }
      } else if (word.j) {
        speaker0 = word.j
      }
    }

    return false;
  }

  static findCaptionForSec(captions, sec) {
    //console.log('looking for sec ', sec);
    if (!captions || captions.length==0) {
      return ["",0,0,0];
    }

    if (captions.length==1 || sec<=captions[0][1]) {
      return captions[0];
    }

    for (let i=0; i<captions.length-1; i++) {
      let time0 = captions[i][1];
      let time1 = captions[i+1][1];

      if (sec>=time0 && sec<=time1) {
        return captions[i];
      }
    }

    return captions[captions.length-1];
  }

  static isPotentiallyDirectMediaLink(url) {
    if (!url) {
      return false;
    }

    url = url.toString();

    if (url.startsWith("https://") || url.startsWith("http://")) {
      if (Helpers.isYouTubeUrl(url)) {
        return false;
      }

      return true;
    } else {
      return false;
    }
  }

  static isYouTubeUrl(url) {
    if (!url) {
      return false;
    }

    if (url.toLowerCase().indexOf("youtube.com")>=0 || url.toLowerCase().startsWith("https://youtu.be/")) {
      return true;
    }

    return false;
  }

  static getYouTubeIdFromUrl(url) {
    if (!url) {
      return "";
    }

    url = url.toString();

    if (Helpers.isYouTubeUrl(url)) {
      let regExp = /.*(?:youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=)([^#\&\?]*).*/;
      let match = url.match(regExp);
      return (match&&match[1].length==11)? match[1] : "";
    }

    return "";
  }

  static elementToCaptions(el) {
    //console.log('got html el: ', el);
    let captions = [];

    if (el instanceof HTMLElement) {
      if (el.childElementCount > 0 && el.className.indexOf("wordSpan")==-1) {
        for (const iEl of el.children) {
          captions.push( ...Helpers.elementToCaptions(iEl));
        }
      } else if (el.textContent.trim().length>0) {
        let caption = [];

        caption[0] = el.innerText.trim();

        // data-s={caption[1]} data-e={caption[2]} data-j={caption[3]} data-t={caption[0]}
        if (el.hasAttribute('data-s')) {
          caption[1] = Number.parseFloat(el.getAttribute("data-s"));
        }

        if (el.hasAttribute('data-e')) {
          caption[2] = Number.parseFloat(el.getAttribute("data-e"));
        }

        if (el.hasAttribute('data-j')) {
          caption[3] = Number.parseFloat(el.getAttribute("data-j"));
        }

        captions.push(caption);
      }
    }

    return captions;
  }

  static captionsToInnerHtml(captions, classes, shouldNotIncludeHeaders) {
    return ReactDOMServer.renderToStaticMarkup(Helpers.captionsToSpans(captions, classes,shouldNotIncludeHeaders));
  }

  static captionsToSpans(captions, classes, shouldNotIncludeHeaders, noTimes, noSpeakerTags) {
    let spans = [];
    let isSingleSpeaker = !Helpers.hasMultipleSpeakers(captions);
    let isTextOnly = !Boolean(classes);
    if (!shouldNotIncludeHeaders) {
      if (noTimes && noSpeakerTags) {
        shouldNotIncludeHeaders = true;
      }
    }

    if (captions.length==0) {
      if (isTextOnly) {
        return "";
      } else {
        return <span></span>
      }
    }

    let firstCaption = captions[0];
    if (!shouldNotIncludeHeaders) {
      if (!noSpeakerTags) {
        if (!isSingleSpeaker) {
          let text = `Speaker ${firstCaption[3]}:  `;
          if (isTextOnly) {
            spans.push(text)
          } else {
            spans.push(<span className={(classes.transcriptSectionHeader)}
                             style={{color: Consts.speakerColors[firstCaption[3] - 1]}}>{text}</span>);
          }
        }
      }
      if (!noTimes) {
        let text = `{ ${GenericUtils.millisecondsToReadableTimestamp(1000 * firstCaption[1])} }`;
        if (isTextOnly) {
          spans.push(text)
        } else {
          spans.push(<span className={(classes.transcriptSectionHeader)}>{text}</span>);
        }
      }

      if (isTextOnly) {
        spans.push("\n");
      } else {
        spans.push(<br/>);
      }
    }
    spans.push(
      Helpers.captionToSpan(firstCaption, classes, "", true)
    );

    for (let i=1; i<captions.length; i++) {
      let iCaption = captions[i];
      let i_1Caption = captions[i-1];
      let dt = iCaption[1]-i_1Caption[2];
      //let marginLeft = 5 + 5*dt;
      let spaces = "";
      if (iCaption[3]!=i_1Caption[3]) {
        let text = shouldNotIncludeHeaders || noSpeakerTags ? "" : `Speaker ${iCaption[3]}:  `+ (noTimes ? "" : `{ ${GenericUtils.millisecondsToReadableTimestamp(1000 * iCaption[1])} }`);
        if (isTextOnly) {
          spans.push("\n\n" + text + "\n");
        } else {
          spans.push(<br/>);
          spans.push(<br/>);
          spans.push(<span style={{color: Consts.speakerColors[iCaption[3] - 1]}}
                           className={(classes.transcriptSectionHeader)}>{text}</span>);
          spans.push(<br/>);
        }
      } else if (dt>10) {
        let text = shouldNotIncludeHeaders || noTimes ? "" : `{ ${GenericUtils.millisecondsToReadableTimestamp(1000*iCaption[1])} }\n`;
        if (isTextOnly) {
          spans.push("\n\n" + text);
        } else {
          spans.push(<br/>);
          spans.push(<br/>);
        }
      } else if (dt>5) {
        if (isTextOnly) {
          spans.push("\n");
        } else {
          spans.push(<br/>);
        }
      } else if (!shouldNotIncludeHeaders && !noTimes && GenericUtils.shouldInsertTimeStamp(1, i_1Caption[2], iCaption[2])) {
        let text = shouldNotIncludeHeaders ? "" : `{ ${GenericUtils.millisecondsToReadableTimestamp(1000*iCaption[1])} }`;
        if (isTextOnly) {
          spans.push("\n\n" + text + "\n");
        } else {
          spans.push(<br/>);
          spans.push(<br/>);
          spans.push(<span className={(classes.transcriptSectionHeader)}>{text}</span>);
          spans.push(<br/>);
        }
      } else {
        spaces = " ";
        while (dt > 1) {
          spaces += " ";
          dt -= 0.5;
        }
      }

      let isToIncludeVisibleTimestamp = ((i%1)==0);
      spans.push(Helpers.captionToSpan(iCaption, classes, spaces, isToIncludeVisibleTimestamp));
    }
    if (isTextOnly) {
      spans.push("\n\n\n");
    } else {
      spans.push(<br/>);
      spans.push(<br/>);
      spans.push(<br/>);
    }
    {
      let text = shouldNotIncludeHeaders ? "" : ` ---   End of transcript   --- `;
      if (isTextOnly) {
        spans.push(text);
      } else {
        spans.push(<span className={(classes.transcriptSectionHeader)}
                         style={{fontSize: "small"}}><i>{text}</i></span>);
      }
    }
    if (isTextOnly) {
      spans.push("\n\n\n");
    } else {
      spans.push(<br/>);spans.push(<br/>);spans.push(<br/>);spans.push(<br/>);spans.push(<br/>);
    }

    return spans;
  }

  static captionToSpan(caption, classes, spaces ) {
    //isToIncludeTimelineTimestamp = isToIncludeTimelineTimestamp && /Chrome/.test(navigator.userAgent);
    let isTextOnly = !Boolean(classes);
    let text = spaces + caption[0];
    if (isTextOnly) {
      return text;
    } else {
      let humanTimeStamp = GenericUtils.millisecondsToReadableTimestamp(1000*caption[1]);
      let leftMargin = "-70px";  // From humanTimeStamp that has 4 chars. For every additional char add 10px to -70px;
      if (humanTimeStamp.toString().length==5) {
        leftMargin = "-80px"
      } else if (humanTimeStamp.toString().length==6) {
        leftMargin = "-90px"
      } else if (humanTimeStamp.toString().length==7) {
        leftMargin = "-100px"
      }
      return <span className={(classes.wordSpan) + " wordSpan"} data-timestamp={GenericUtils.millisecondsToReadableTimestamp(caption[1]*1000)} data-s={caption[1]} data-e={caption[2]} data-j={caption[3]} data-t={caption[0]} style={{color: Consts.speakerColors[caption[3]-1]}} >{text}</span>
    }
  }

  static oldJsonFormatToCaptions(jsonString) {
    if (!jsonString) {
      return [];
    }
    let transcriptSegments = JSON.parse(jsonString);
    if (transcriptSegments.length==0) {
      return [];
    }
    let captions = [];

    for (let i=0; i<transcriptSegments.length; i++) {
      let maxDurationMs = null;
      let segment = transcriptSegments[i];
      if (i<transcriptSegments.length-1) {
        maxDurationMs = (transcriptSegments[i+1].time - segment.time) * 1000 -1;
      } else {
        maxDurationMs = Helpers.calculateDurationMs(segment.text, 1000000);
      }

      let startTime = segment.time;
      let endTime = segment.endTime || (segment.time + (maxDurationMs/1000));
      let txt = segment.text || "";

      let newCaption = [txt, startTime, endTime, 0];
      captions.push(newCaption);
    }

    return captions;
  }

  static exportCaptions(captions, title, type) {
    let noTimes = !StoreRepo.getStore().getState().prefTimesReducer;
    let noSpeakerTags = !StoreRepo.getStore().getState().prefSpeakersReducer;

    if (type == "txt") {
      let textToDownload = Helpers.captionsToDownloadableText(captions, noTimes, noSpeakerTags);
      Helpers.actuallyDownloadTextAsFile(textToDownload, title+"_transcript.txt");
    } else if (type == "srt") {
      let textToDownload = Helpers.captionsToSrt(captions);
      Helpers.actuallyDownloadTextAsFile(textToDownload, title+"_captions.srt");
    } else if (type == "pdf") {
      Helpers.printCaptions(captions, title, {something: "something"}, noTimes, noSpeakerTags);
    } else if (type == "copy") {
      Helpers.copyCaptions(captions, title, noTimes, noSpeakerTags);
    } else if (type == "docx") {
      alert("Export to docx is under development. In the meanwhile - simply 'copy' using the Copy button and paste into a new Word document.");
    } else if (type == "other") {
      alert ("Need a different export format? Let us know at admin@speechlogger.com");
    } else {
      alert ("Export not yet supported. Please contact us at admin@speechlogger.com to inform us you need this.");
    }
  }

  static printCaptions(captions, title, classes, noTimes, noSpeakerTags) {
    let el = document.createElement("div");
    let spans = Helpers.captionsToSpans(captions, classes, false, noTimes, noSpeakerTags);
    const htmlString = ReactDOMServer.renderToStaticMarkup(
      <div>
      <h1>{title}</h1>
      <p style={{position: "relative", marginLeft: (/Chrome/.test(navigator.userAgent)) ? "100px" : "10px", padding: "5px"}}>
        {spans}
      </p>
      </div>
    );
    el.innerHTML = htmlString;
    Helpers.printDiv(el, title)
  }

  static copyCaptions(captions, title, noTimes, noSpeakerTags) {
    let el = document.createElement("div");
    let spans = Helpers.captionsToSpans(captions, false, false, noTimes, noSpeakerTags);
    const htmlString = ReactDOMServer.renderToStaticMarkup(
      <div style={{whiteSpace: "pre-wrap",}}>
        <p style={{position: "relative", marginLeft: (/Chrome/.test(navigator.userAgent)) ? "100px" : "10px", padding: "5px",whiteSpace: "pre-wrap",}}>
          {spans}
        </p>
        <p>{" ---   " + title + "   --- "}</p>
      </div>
    );
    el.innerHTML = htmlString;
    Helpers.copyTextToClipboard(el.innerText);
  }

  static printDiv(el, title) {
    let mywindow = window.open('', 'PRINT', 'height=650,width=900,top=100,left=150');

    mywindow.document.write(`<html><head><title>${title}</title>`);
    mywindow.document.write("<style>.timeStampOnTimeline {" +
      "top: 0;\n" +
      "    color: darkgray;\n" +
      "    display: block;\n" +
      "    z-index: 999;\n" +
      "    position: absolute;\n" +
      "    margin-left: -70px;\n" +
      "    background-color: white;" +
      "}" +
      ".timeStampTooltip {" + "display: none;}" +
      ".wordSpan {" + "display: inline;\n" +
      "    position: relative;\n" +
      "    font-size: large;\n" +
      "    white-space: pre-wrap;}" +

      "</style>");
    mywindow.document.write('</head><body >');
    mywindow.document.write(el.innerHTML);
    mywindow.document.write('</body></html>');
    mywindow.document.close(); // necessary for IE >= 10
    mywindow.focus(); // necessary for IE >= 10*/
    mywindow.print();

    return true;
  }

  static copyEl(el) {
    window.getSelection().selectAllChildren(
      el
    );
    document.execCommand("copy");
  }

  static fallbackCopyTextToClipboard(text) {
    var textArea = document.createElement("textarea");
    textArea.value = text;

    // Avoid scrolling to bottom
    textArea.style.top = "0";
    textArea.style.left = "0";
    textArea.style.position = "fixed";

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    try {
      var successful = document.execCommand('copy');
      var msg = successful ? 'successful' : 'unsuccessful';
      console.log('Fallback: Copying text command was ' + msg);
    } catch (err) {
      console.error('Fallback: Oops, unable to copy', err);
    }

    document.body.removeChild(textArea);
  }

  static copyTextToClipboard(text) {
    if (!navigator.clipboard) {
      Helpers.fallbackCopyTextToClipboard(text);
      return;
    }
    navigator.clipboard.writeText(text).then(function() {
      alert('Copied successfully. Now you may paste the transcript to any text editor (eg Word, Pages, etc)');
    }, function(err) {
      console.error('Async: Could not copy text: ', err);
    });
  }

}