import UploadFileJob from "../model/UploadFileJob";
import UploadState from "../model/UploadState";
import StoreRepo from "./StoreRepo";
import {setUploadAction} from "../redux/actions";

export default class UploadQueueFifo {

  static #_fifo:UploadFileJob[] = [];
  static onSuccess:function = null;
  static onFail:function = null;
  static onProgress:function = null;

  static getQueue = function():UploadFileJob[] {
    return [ ...UploadQueueFifo.#_fifo];
  }

  static jobIdExistsAlready = function(id:number) {
    for (const queueElement of UploadQueueFifo.getQueue()) {
      if (id==queueElement.jobId) {
        return true;
      }
    }

    return false;
  }

  static push = function (job:UploadFileJob) {
    if (job==null){
      return;
    }

    if (Number.isNaN(job.jobId) || job.jobId<0 ) {
      job.jobId = Math.floor(Math.random() * 1000000000);
    }

    while (UploadQueueFifo.jobIdExistsAlready(job.jobId)) {
      job.jobId = Math.floor(Math.random() * 1000000000);
    }

    UploadQueueFifo.#_fifo.push(job);
    UploadQueueFifo.uploadNext();
  }

  static isNext = function():boolean {
    if (UploadQueueFifo.#_fifo.length==0) {
      return false;
    } else {
      return true;
    }
  }

  static pop = function ():UploadFileJob {
    if (!UploadQueueFifo.isNext()) {
      return null;
    } else {
      return UploadQueueFifo.#_fifo.shift();
    }
  }

  static peek = function ():UploadFileJob {
    if (!UploadQueueFifo.isNext()) {
      return null;
    } else {
      return UploadQueueFifo.#_fifo[0];
    }
  }

  static uploadNext = function() {
    if (UploadQueueFifo.#_fifo.length==0) {
      UploadQueueFifo.updateUi();
      return;
    }

    if (Number.isNaN(UploadQueueFifo.#_fifo[0].progress)) {
      UploadQueueFifo.#_fifo[0].upload(UploadQueueFifo.callbacks.onSuccess, UploadQueueFifo.callbacks.onFail, UploadQueueFifo.callbacks.onProgress );
    }
    UploadQueueFifo.updateUi();
  }

  static callbacks = {
    onSuccess: function() {
      // pop:
      let job = UploadQueueFifo.pop();
      if (UploadQueueFifo.onSuccess!=null) {
        UploadQueueFifo.onSuccess(job);
      }
      UploadQueueFifo.uploadNext();
    },

    onFail: function() {
      // pop:
      let job = UploadQueueFifo.pop();
      if (UploadQueueFifo.onFail!=null) {
        UploadQueueFifo.onFail(job);
      }
      alert('Failed to upload file ' + job.file.name);
      //Api.sendChatData('Auto generated - need human support - file upload failed on client side');

      UploadQueueFifo.uploadNext();
    },

    onProgress: function(progress) {
      if (UploadQueueFifo.onProgress!=null) {
        UploadQueueFifo.onProgress(progress, UploadQueueFifo.peek());
      }
      UploadQueueFifo.updateUi();
    }
  }

  static updateUi() {
    let currentQueue = UploadQueueFifo.getQueue();
    // Digest data:
    let numberOfFiles:number = currentQueue.length;
    let isUploading:boolean = numberOfFiles>0;
    let currentFileProgress:number;

    if (!UploadQueueFifo.isNext()) {
      currentFileProgress = 0;
    } else {
      currentFileProgress = UploadQueueFifo.peek().progress;
    }

    if (Number.isNaN(currentFileProgress)) {
      currentFileProgress = 0;
    }

    let uploadState:UploadState = new UploadState(numberOfFiles, currentFileProgress, isUploading);

    StoreRepo.getStore().dispatch(setUploadAction(uploadState));
  }
}