import Typography from "@material-ui/core/Typography";
import LinearProgress from "@material-ui/core/LinearProgress";
import React from "react";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";

const styles = theme => ({
  fixedNotification: {
    position: "fixed",
    top: "40px",
    left: "50px",
    right: "50px",
    padding: "20px 50px",
    textAlign: "center",
    backgroundColor: "#ff6b6b",
  },
  fixedNotificationGreen: {
    position: "fixed",
    top: "40px",
    left: "50px",
    right: "50px",
    padding: "20px 50px",
    textAlign: "center",
    backgroundColor: "#00cc55",
  },
  fixedNotificationText: {
    color: "#fff",
  },
  congrats: {
    color: 'green'
  }
});

class UploadStatusBar extends React.Component {

render() {
  const {classes, t, i18n, uploadState } = this.props;

  // numberOfFiles:number = 0;
  //   progressCurrentFile
  
  return uploadState.isUploading ?
        <div className={classes.fixedNotification}>
          <Typography className={classes.fixedNotificationText} variant="h6">
            {t('hero.uploading')} {uploadState.numberOfFiles} files. {t('hero.do not close')}
          </Typography>
          <br/>
          <LinearProgress variant="determinate" value={uploadState.progressCurrentFile*100/(uploadState.numberOfFiles)} />
        </div> : <div/>
    }
  
}

export default withStyles(styles)(withTranslation()(UploadStatusBar));