export class MyFirebase {

  static init() {
    let config = {
      apiKey: "AIzaSyDS4OUr6QMADFbbFbXpfYnsDkpk-jHOuO0",
      authDomain: "speechnotes.co",
      databaseURL: "https://speechnotes-bba59.firebaseio.com",
      storageBucket: "speechnotes-bba59.appspot.com",
      projectId: "speechnotes-bba59",
    };

    firebase.initializeApp(config);
  }

  // Configure FirebaseUI for login screen.
  static uiConfig = {
    // Popup signin flow rather than redirect flow.
    signInFlow: 'popup',
    // We will display Google and Facebook as auth providers.
    signInOptions: [
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      //firebase.auth.FacebookAuthProvider.PROVIDER_ID
    ],
    callbacks: {
      // Avoid redirects after sign-in.
      signInSuccessWithAuthResult: () => false
    }
  };

  static auth = firebase.auth();
  //static storage = firebase.storage;


  static getDbSamplesRef() {
    return firebase.firestore().collection("samples").doc("emptyUser");
  }

  static getDbUserRef() {
    if (MyFirebase.user!=null && MyFirebase.user.uid!=null) {
      return firebase.firestore().collection("users").doc(MyFirebase.user.uid);
    }

    return null;
  }

  static getUserId() {
    if (MyFirebase.user!=null && MyFirebase.user.uid!=null) {
      return MyFirebase.user.uid;
    }

    return null;
  }

  static getDbTime() {
    return firebase.firestore.FieldValue.serverTimestamp()
  }

  static logError(inFunction, errorMsg) {
    if (!MyFirebase.user) {
      return;
    }

    firebase.firestore().collection("clientErrors").doc(MyFirebase.getUserId() + "_" + GenericUtils.generateRandomString(5)).set({
      by: MyFirebase.getUserId(),
      modifiedAt: MyFirebase.getDbTime(),
      error: errorMsg ? errorMsg : "unspecified",
      inFunction: inFunction ? inFunction : "unspecified"
    }).then((doc) => {

    }).catch(() => {
      //console.log('failed to update doc no db');
    });
  }



  static getStorageRootRef() {
    return firebase.storage().ref();
  }

  static user; // Of type Firebase.User (used mainly for user.uid)

  static signin() {
    firebase.auth().signInWithPopup(provider).then(function(result) {
      // This gives you a Google Access Token. You can use it to access the Google API.
      let token = result.credential.accessToken;
      // The signed-in user info.
      let user = result.user;
      let isNewUser = result.additionalUserInfo.isNewUser;
      if (isNewUser) {
        Api.sendWelcomeEmail(()=>{}, ()=>{
          StoreRepo.getStore().dispatch(setAlertAction({title: "Oops... - error connecting our server", body: "It seems there is an error connecting our server. It is likely due to a firewall on your side. If you do use a firewall - please whitelist our server address: 'https://itranscribe.app:8443/api3'. For assistance - contact us at admin@speechlogger.com"}));
        });
      }
      // ...
    }).catch(function(error) {
      // Handle Errors here.
      let errorCode = error.code;
      let errorMessage = error.message;
      // The email of the user's account used.
      let email = error.email;
      // The firebase.auth.AuthCredential type that was used.
      let credential = error.credential;
      // ...
    });
  }

}

export var auth = MyFirebase.auth;

window.wsGlobals = window.wsGlobals || {};
window.wsGlobals.MyFirebase = MyFirebase;