import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import {withTranslation} from "react-i18next";


const styles = theme => ({
  root: {
    display: "inline-block",
    textAlign: "center",
    fontSize: "30px",
    border: "1px solid gray",
    borderRadius: "20px",
  },
  btn: {
    backgroundColor: "transparent",
    color: "blue",
    fontWeight: "bold",
    border: "none",
    outline: "none",
    padding: "0 20px",
    ':focus': {
      border: "none",
      outline: "none",
    }
  },
  content: {
    minWidth: "120px",
    padding: "0 20px",
    display: "inline-block",
  }
});

class StepPicker extends React.Component {

  state = {
    step: 0,
  };

  componentDidMount() {
    if (this.props.defaultStep) {
      this.setState({step:this.props.defaultStep});
    }
  }

  handlePlusMinusClick = (addOrSubtract) => {
    let newIndex = addOrSubtract >0 ? this.state.step+1 : this.state.step-1;
    if (this.props.steps && this.props.steps.length > newIndex && newIndex>=0) {
      if (this.props.onChange) {
        this.props.onChange(newIndex);
      }
      this.setState({step: newIndex});
    }
  };

  render() {
    const {classes, t, i18n, steps } = this.props;

    if (!steps) {
      return;
    }

    return <div className={classes.root}>
      <button className={classes.btn} style={{borderRight: "1px lightblue solid"}} onClick={()=>{this.handlePlusMinusClick(-1)}}>-</button>
      <span className={classes.content} >{steps[this.state.step]}</span>
      <button className={classes.btn} style={{borderLeft: "1px lightblue solid"}} onClick={()=>{this.handlePlusMinusClick(+1)}}>+</button>
    </div>
  }
}

StepPicker.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(StepPicker));