import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import DialogTitle from "@material-ui/core/DialogTitle";
import {Typography} from "@material-ui/core";
import MyPayPalButton from "./MyPayPalButton";
import Radio from "@material-ui/core/Radio";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormLabel from "@material-ui/core/FormLabel";
import FormControl from "@material-ui/core/FormControl";
import RadioGroup from "@material-ui/core/RadioGroup";
import {withTranslation} from "react-i18next";


const styles = theme => ({
  formControl: {
    margin: theme.spacing.unit * 3,
  },
  group: {
    margin: `${theme.spacing.unit}px 0`,
  },
});

class SwitchPaypalDialog extends React.Component {

  componentDidMount() {

  }

  handleChange = event => {
    this.setState({
      minutes: parseInt(event.target.value),
    });
  };

  render() {
    const {classes, t, i18n } = this.props;

    if (this.props.open) {
      return <Dialog
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
      >
        <DialogTitle id="scroll-dialog-title">PayPal Account Verification</DialogTitle>
        <DialogContent>

          <Typography variant="subtitle1" align="center" paragraph>
            In order to verify your PayPal account, transfer $0.1 from the PayPal account you want to withdraw funds to.
          </Typography>


          <div style={{textAlign: "center"}} >
            <MyPayPalButton totalInDollars={0.1} onSuccess={() => {this.props.onSuccess(); this.props.onClose(); }}/>
          </div>

        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="primary">
            {t('cancel')}
          </Button>
        </DialogActions>
      </Dialog>
    }

    return <div/>
  }
}

SwitchPaypalDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(SwitchPaypalDialog));