// The strings are defined by google:
const FILE_TYPE_REGULAR = "default";
const FILE_TYPE_PHONE = "phone_call";
const FILE_TYPE_VIDEO = "video";

const secondsToMinutes = seconds => Math.floor(seconds / 60) + ':' + ('0' + Math.floor(seconds % 60)).slice(-2);

const styles = theme => ({
  root: {
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  hidden: {
    display: "none",
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
  urlInput: {
    border: 'solid 1px gray',
  },
  actionsContainer: {
    textAlign: "right",
  },
  selectFileButton: {
    cursor: "pointer",
    color: theme.palette.primary.main
  }
});

class RemoveAccountDialog extends React.Component {

  state = {
    fileUrl: ""
  };

  handleFileUrlChange = (e) => {
    // https://ttsreader.com/actors/1001/sample1.mp3
    let csUrls = e.target.value;

    if (csUrls==null || csUrls==undefined) {
      return;
    }

    csUrls = csUrls.trim();
    csUrls = csUrls.toLowerCase();

    this.setState({fileUrl: csUrls});
  };

  onSubmit = async (e) => {
    Api.removeAccount(()=>{
      this.props.onClose();
      MyFirebase.auth.signOut();
    },()=>{
      alert("Something went wrong. Please try again or contact admin@speechlogger.com directly.");
    });
  };

  isReadyToSubmit = () => {
    return this.state.fileUrl=="remove";
  }

  render() {
    const {classes, t, i18n } = this.props;

    if (this.props.open) {
      return <div >
        <Dialog
          className={classes.root}
          fullWidth={true}
          maxWidth = {'sm'}
        open={this.props.open}
        onClose={this.props.onClose}
        aria-labelledby="scroll-dialog-title"
      >
        <DialogTitle id="scroll-dialog-title">Verify Permanently Remove Account</DialogTitle>
        <DialogContent>

          <Typography variant="subtitle1" paragraph>
            <span style={{color: "red"}}><b>CAREFUL: </b></span>&nbsp;This will entirely remove your account, will remove all your records from our servers, including your transcripts and remaining dollar balance. This cannot be undone.
          </Typography>
          <Typography variant="subtitle1" paragraph>
            To proceed - type <span style={{color: "black", padding: "5px", borderRadius: "2px", backgroundColor: "#eee"}}><b>REMOVE</b></span>&nbsp; in the following text area:
          </Typography>

          <FormControl className={classes.formControl}>
            <TextareaAutosize aria-label="minimum height" rowsMin={1} className={classes.urlInput} placeholder="REMOVE" name="fileUrl" id="fileUrl" onChange={ (e) => this.handleFileUrlChange(e) } ref={ref => this.urlInputRef = ref} style={{marginBottom: "10px"}} />
          </FormControl>

        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="primary">
            {t('cancel')}
          </Button>
          <Button onClick={this.onSubmit} color="secondary" disabled={!this.isReadyToSubmit()} >
            PERMANENTLY REMOVE ACCOUNT
          </Button>
        </DialogActions>
      </Dialog>


      </div>
    }

    return <div/>
  }

}



RemoveAccountDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(RemoveAccountDialog));