class MyAlert extends React.Component {
  state = {
    anchorEl: null,
    mobileMoreAnchorEl: null,
    isDialogOpen: false,
    isSamplesDialogOpen: false,
  };

  handleOpenDictation = event => {
    const {classes, t, i18n } = this.props;
    window.open(t("links.speechnotes"),"_blank");
  };

  handleOpenDemos = event => {
    if (this.props.isSignedIn) {
      this.setState({isSamplesDialogOpen: true});
    } else {
      let element = document.getElementById("samples");
      element.scrollIntoView({behavior: "smooth"});
    }
  };

  handleClose = () => {
    StoreRepo.getStore().dispatch(setAlertAction(null));
  }

  render() {
    const {classes, t, i18n, alertState } = this.props;

      return (
        <Dialog
          open={Boolean(alertState)}
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{alertState ? alertState.title : ""}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description" style={{whiteSpace: 'pre-line'}}>
              {alertState ? alertState.body : ""}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary" autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>
      );
  }
}

MyAlert.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(MyAlert));