import UploadState from "../../model/UploadState";

export const ACTION_TYPES = {
  signIn: 'SET_SIGNIN',
  alert: 'SET_ALERT',
  uploadState: 'SET_UPLOAD_STATE',
  balanceState: 'SET_BALANCE_STATE',
  userPrefPrintTimes: 'SET_PRINT_TIMES',
  userPrefPrintSpeakers: 'SET_PRINT_SPEAKERS',
}

export const setSigninAction = isSignedIn => ({
  type: ACTION_TYPES.signIn,
  isSignedIn,
});

export const setAlertAction = alertState => ({
  type: ACTION_TYPES.alert,
  alertState,  // {title: ..., body: ... }
});


export const setUploadAction = (uploadState:UploadState) => ({
  type: ACTION_TYPES.uploadState,
  uploadState,
});


export const setBalanceAction = (balance:Number) => ({
  type: ACTION_TYPES.balanceState,
  balance,
});

export const setUserPrefPrintTimesAction = (yesNo:Boolean) => ({
  type: ACTION_TYPES.userPrefPrintTimes,
  yesNo,
});

export const setUserPrefPrintSpeakersAction = (yesNo:Boolean) => ({
  type: ACTION_TYPES.userPrefPrintSpeakers,
  yesNo,
});





let nextTodoId = 0;



export const addTodo = text => ({
  type: 'ADD_TODO',
  id: nextTodoId++,
  text
})

export const setVisibilityFilter = filter => ({
  type: 'SET_VISIBILITY_FILTER',
  filter
})

export const toggleTodo = id => ({
  type: 'TOGGLE_TODO',
  id
})

export const VisibilityFilters = {
  SHOW_ALL: 'SHOW_ALL',
  SHOW_COMPLETED: 'SHOW_COMPLETED',
  SHOW_ACTIVE: 'SHOW_ACTIVE'
}
