import React, { useRef } from 'react';
// First way to import
import {Button, TextField, Typography, withStyles} from "@material-ui/core";
import {withTranslation} from "react-i18next";
import {Api} from "../network/Api";
import Snackbar from "@material-ui/core/Snackbar";
import SnackbarContent from "@material-ui/core/SnackbarContent";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from '@material-ui/icons/Info';
import CopyIcon from '@material-ui/icons/FileCopy';
import CloseIcon from '@material-ui/icons/Close';
import CheckIcon from '@material-ui/icons/Check';
import {amber, green} from "@material-ui/core/colors";
import Loading from "./Loading";
import LinearProgress from "@material-ui/core/LinearProgress";
import Link from "@material-ui/core/Link";

const styles = theme => ({
  root: {
    justifyContent: 'center'
  },
  success: {
    backgroundColor: green[600],
  },
  error: {
    backgroundColor: theme.palette.error.dark,
  },
  info: {
    backgroundColor: theme.palette.primary.main,
  },
  warning: {
    backgroundColor: amber[700],
  },
  icon: {
    fontSize: 20,
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing.unit * 1,
  },
  message: {
    display: 'flex',
    alignItems: 'center',
  },
  linkBox: {
    display: 'flex',
    width: '100%',
    alignItems: 'center',
    textAlign: 'center',
  },
  button: {
    display: 'flex',
    margin: 'auto',
    //border: '1px solid blue',
    //color: 'blue',
    alignItems: 'center',
    width: '100%',
  },
  twitterButton: {
    display: 'flex',
    margin: 'auto',
    border: '1px solid #55acee',
    alignItems: 'center',
    width: '100%',
  },
});

class ShareReferrerLink extends React.Component {

  textArea = null;

  state = {
    open: false,
    isGenerating: false,
  };

  generateLink = () => {
    Api.generateReferrerId(null, null);
    this.setState({isGenerating: true});
  };

  copyToClipboard = (e) => {
    this.textArea.select();
    document.execCommand('copy');
    // This is just personal preference.
    // I prefer to not show the the whole text area selected.
    e.target.focus();
    this.setState({open: true});
  };

  selectAll = (e) => {
    this.textArea.select();
  };


  render() {

    const {classes, t, i18n, referrerId, isAffiliate } = this.props;

    if (referrerId==null || referrerId==undefined || referrerId=='null' || referrerId=='undefined' || referrerId.length==0) {
      return <div className={classes.root}>
        { this.state.isGenerating ? <LinearProgress /> :
          <Button variant="outlined" color={'primary'} className={classes.button}
                  onClick={this.generateLink}>{t('new.generate referrerId')}
          </Button>
        }
      </div>
    }

    let link = "https://speechnotes.co/files/?ref=" + (isAffiliate ? "a" : "f") + referrerId ;
    let promo = encodeURI("Get $5 added bonus on your first purchase for automatic transcription using this link");
    //let promo = encodeURI("New: automatic transcription using Google's AI for just $0.1 a minute!");

    return (
      <div className={classes.root}>
        <input
          readOnly={true}
          className={classes.linkBox}
          type={'text'}
          ref={(textarea) => this.textArea = textarea}
          value={link}
          onClick={this.selectAll}
        />
        <br/>
        {!isAffiliate ? <div/> :
          <Typography variant="caption" align="center" paragraph>
            By sharing the link you agree to the program's <Link target={'_blank'} href={'https://speechnotes.co/files/affiliates#terms'}>terms</Link>
          </Typography>
        }
        <Button variant="outlined" color={'primary'} className={classes.button} onClick={this.copyToClipboard}><CopyIcon className={classes.icon} /> &nbsp;{t('new.copy')}</Button>
        <br/>
        {isAffiliate ? <div /> : <div>
        <Button variant="contained" color={'primary'} className={classes.button} onClick={() => {}}>
        <a style={{color: 'white'}} href={"https://www.facebook.com/sharer/sharer.php?u=" + link} target="_blank">
          {t('new.share on fb')}
        </a>
        </Button>
        <br/>
        <Button variant="outlined" className={classes.twitterButton} onClick={() => {}}>
        <a style={{color: '#55acee'}} href={"https://twitter.com/intent/tweet?text=" + promo + "%20" + link} target="_blank">
          {t('new.share on twitter')}
        </a>
        </Button>
        </div>}
        {/*<Button variant="contained" color={'primary'} className={classes.button} onClick={() => {this.shareToFb(link);}}>{t('new.share on fb')}</Button>*/}
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={3000}
          onClose={() => this.setState({open: false})}
        >
          <SnackbarContent
            className={classes.success}
            aria-describedby="client-snackbar"
            message={
              <span id="client-snackbar" className={classes.message}>
          <CheckIcon className={classes.icon} />
          &nbsp;
                {t('new.copied success')}
        </span>
            }
            action={[
              <IconButton key="close" aria-label="Close" color="inherit" onClick={() => this.setState({open: false})}>
                <CloseIcon className={classes.icon} />
              </IconButton>,
            ]}
          />
        </Snackbar>
      </div>
    );
  }
}

export default withStyles(styles)(withTranslation()(ShareReferrerLink));