export default class UploadState {

  numberOfFiles:number = 0;
  progressCurrentFile:number = 0;
  isUploading:boolean = false;


  constructor(numberOfFiles: number, progressCurrentFile: number, isUploading: boolean) {
    this.numberOfFiles = numberOfFiles;
    this.progressCurrentFile = progressCurrentFile;
    this.isUploading = isUploading;
  }
}