import React from "react";

export default class ChatBotNode {

  predefinedId:String;
  title:String;
  messages:String[];
  options:ChatbotNode[];
  predefinedTrigger:String;

  constructor(predefinedId:String, title: String, messages: String[], options: ChatbotNode[], predefinedTrigger: String) {
    this.title = title;
    this.messages = messages;
    this.options = options;
    this.predefinedTrigger = predefinedTrigger;
  }

  toSteps = (ancestorOptionsId:String, parentId:String, predefinedIdForFirstChild:String) => {
    let steps = [];
    let counter = 0;
    let pre = predefinedIdForFirstChild || parentId;

    // steps.push ( all messages + options' titles as a step + all its option's steps. )
    if (this.messages && this.messages.length>0) {
      for (let i=0; i<this.messages.length; i++) {
        counter++;
        let id = (counter==1 && predefinedIdForFirstChild) ? predefinedIdForFirstChild : pre + "." + counter;
        let triggerId = pre + "." + (counter+1);
        steps.push(ChatBotNode.messageToStep(this.messages[i], id, triggerId))
      }
    }

    if (this.options && this.options.length>0) {
      // Build options step:
      let options = [];
      counter++;
      let id = (counter==1 && predefinedIdForFirstChild) ? predefinedIdForFirstChild : pre + "." + counter;

      for (let i=0; i<this.options.length; i++) {
        let childId = id + "." + i;
        let option = this.options[i];
        if ((option.messages && option.messages.length>0) || (option.options && option.options.length>0)) {
          steps.push(...this.options[i].toSteps(id, id, childId))
          options[i] = {
            label: option.title,
            trigger: childId,
            value: i
          }
        } else {
          options[i] = {
            label: option.title,
            trigger: ancestorOptionsId,
            value: i
          }
        }
      }
      steps.push(ChatBotNode.optionsToStep(options, id));
    } else {

      if (this.predefinedTrigger) {
        steps[steps.length-1].trigger = this.predefinedTrigger;
      } else {
        counter++;
        let endOptions = [
          {label: "Ask more on this topic", value: 0, trigger: ancestorOptionsId},
          {label: "Do something else", value: 1, trigger: "main"}
        ]

        steps.push({
          id: pre + "." + counter,
          options: endOptions
        })
      }
    }

    return steps;
  }

  static messageToStep(text:String, id:String, trigger:String):Object {
    if (!text) {
      return;
    }

    text = text.trim();

    if ((text.startsWith("https://") || text.startsWith("http://")) && text.indexOf(" ")==-1 ) {
      return {
        id: id,
        component: (<div>
          <a href={text} target={"_blank"}>{text}</a>
        </div>),
        trigger: trigger
      }
    }

    return {
      id: id,
      message: text,
      trigger: trigger
    }
  }

  static optionsToStep(options:Object[], id:String):Object {
    return {
      id: id,
      options: options,
    }
  }
}