import React, {Component, Suspense} from 'react';
import './App.css';

import {auth, MyFirebase} from "./network/MyFirebase";
import Scaffold from "./screens/Scaffold";
import {setSigninAction} from "./redux/actions";
import SigninScreen from "./screens/SigninScreen";
import Loading from "./components/Loading";
import {Api} from "./network/Api";
import StoreRepo from "./repos/StoreRepo";


class App extends Component {
  // The component's Local state.
  state = {
    isSignedIn: false // Local signed-in state.
  };

  // Listen to the Firebase Auth state and set the local state.
  componentDidMount() {
    const urlParams = new URLSearchParams(window.location.search);
    const referredBy = urlParams.get('ref');

    StoreRepo.setStore(this.props.store);

    if (referredBy!=null && referredBy!=undefined && referredBy.length>0) {
      //console.log('referred by ' + referredBy);
      window.localStorage.setItem('referredBy', referredBy);
    }

    this.unregisterAuthObserver = auth.onAuthStateChanged(
      (user) => {
        // user is of type Firebase.User which extends UserInfo
        if (user==null) {
          console.log("signed out");
          //window.location = "../_test/" // TODO: ...
        } else {
          console.log(user.uid);
          MyFirebase.user = user;
          const referredBy = window.localStorage.getItem('referredBy');  // Returns null if not there.
          if (referredBy!=null && referredBy!=undefined && referredBy.length>0 && referredBy!='null' && referredBy!='undefined') {
            Api.setReferredBy(referredBy, null, null); // TODO: set state?
          }

        }
        this.setState({isSignedIn: !!user});
        this.props.store.dispatch(setSigninAction(!!user));
      }
    );
    //this.addDomListeners();
  }

  // Make sure we un-register Firebase observers when the component unmounts.
  componentWillUnmount() {
    this.unregisterAuthObserver();
    StoreRepo.setStore(null);
  }

  render() {
    return <Suspense fallback={<Loading/>}>
      {(this.state.isSignedIn) ? <Scaffold /> : <SigninScreen/>}

    </Suspense>
  }
}

export default App;
