import React from 'react';
import PropTypes from 'prop-types';
import {fade} from '@material-ui/core/styles/colorManipulator';
import {withStyles} from '@material-ui/core/styles';
import AccountCircle from '@material-ui/icons/AccountCircle';
import LogoutIcon from '@material-ui/icons/ExitToApp';
import SettingsIcon from '@material-ui/icons/Settings';

import {MyFirebase} from "../network/MyFirebase";
import {AppBar, IconButton, Menu, MenuItem, Toolbar} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import {withTranslation} from "react-i18next";
import MenuIcon from "@material-ui/icons/Menu";
import {MessageOutlined} from "@material-ui/icons";
import BackIcon from "@material-ui/icons/ArrowBackIos";

const styles = theme => ({
  root: {
    width: '100%',
    //position: "fixed",
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    //textTransform: 'uppercase'
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  navLink: {
    //marginRight: 20,
    cursor: "pointer",
    fontWeight: "bold",
    color: "white"
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing.unit * 2,
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit * 3,
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing.unit * 9,
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
});

class MyAppBar extends React.Component {
  state = {
    anchorEl: null,
    isDialogOpen: false,
    isSamplesDialogOpen: false,
  };

  setAnchorEl = (el) => {
    this.setState({anchorEl: el});
  };

  handleClick = (event) => {
    this.setAnchorEl(event.target);
    this.lastTimeOpened = Date.now();
  };

  handleClose = () => {
    this.setAnchorEl(null);
  };

  handleCloseIfDelayedOnly = () => {
    if (this.lastTimeOpened && Date.now()-this.lastTimeOpened>1000) {
      this.handleClose();
    }
  }

  handleOpenDemos = event => {
    if (this.props.isSignedIn) {
      this.setState({isSamplesDialogOpen: true});
    } else {
      let element = document.getElementById("samples");
      element.scrollIntoView({behavior: "smooth"});
    }
  };


  handleMenu = (action) => {
    switch (action) {
      case 'feedback': {
        window.open("https://forms.gle/tEyo7mvwzupHVoTN7", "_blank");
        break;
      }
      case 'signout': {
        this.handleSignOut();
        break;
      }
    }
    this.handleClose();
  }

  handleSignOut = () => {
    MyFirebase.auth.signOut();
  };

  handleSignIn = () => {
    MyFirebase.signin();
    this.handleClose();
  };


  render() {
    const {classes, t, i18n, isSignedIn } = this.props;

    console.log('rendering state: ', this.state);

      return (
        <div className={classes.root}>

          <AppBar>
            <Toolbar style={{display: "flex",color: "white"}}>
              {isSignedIn && <IconButton style={{color: "white"}} onClick={this.props.onLeftMenuClick}>
                <MenuIcon/>
              </IconButton>}

              <span style={{display: "inline-block", flex: 1, textAlign: "right"}}>
                {isSignedIn ?
                  <span>
                    <Button
                      style={{color: "white"}}
                      id={"ws_menu_btn_id_0987skjh"}
                      aria-owns={this.state.anchorEl ? "simple-menu" : undefined}
                      aria-haspopup="true"
                      onClick={this.handleClick}
                      onMouseEnter={this.handleClick}
                    >
        <SettingsIcon/>&nbsp;{this.props.userName}
      </Button>
      <Menu
        style={{minWidth: "300px"}}
        id="simple-menu"
        anchorEl={this.state.anchorEl}
        open={Boolean(this.state.anchorEl)}
        onClose={this.handleClose}
        MenuListProps={{ onMouseLeave: this.handleClose }}
      >
        <MenuItem onClick={this.handleCloseIfDelayedOnly} style={{minWidth: "300px"}}><BackIcon/></MenuItem>
        <hr/>

        <MenuItem onClick={()=>{this.handleMenu('feedback');}}><MessageOutlined/>&nbsp;&nbsp;Feedback</MenuItem>
        <MenuItem onClick={()=>{this.handleMenu('signout');}}><LogoutIcon />&nbsp;&nbsp;{t('nav.signout')}</MenuItem>
      </Menu>
                  </span> :
                  <Button style={{color: "white"}} variant={"success"} onClick={this.handleSignIn} >
                    <AccountCircle/>&nbsp;&nbsp;{t('nav.signin')}
                  </Button>
                }
              </span>
            </Toolbar>
          </AppBar>
        </div>
      );
  }
}

MyAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(MyAppBar));