import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import {withTranslation} from "react-i18next";


const styles = theme => ({
  root: {
    display: "inline-block",
    textAlign: "center",
    fontSize: "20px",
  },
  btn: {
    margin: "5px",
    color: "darkblue",
    borderRadius: "10px",
    outline: "none",
    padding: "5px 10px",
    ':focus': {
      outline: "none",
    }
  },
  selected: {
    border: "2px solid darkblue",
    fontWeight: "bold",
    boxShadow: "2px 2px black",
    backgroundColor: "#3f50b511",
  },
  nonSelected: {
    border: "2px solid lightgray",
    backgroundColor: "#3f50b511",
  }
});

class OffTheShelfPicker extends React.Component {

  state = {
    step: 0,
  };

  componentDidMount() {
    if (this.props.defaultStep) {
      this.setState({step:this.props.defaultStep});
    }
  }

  handleClick = (i) => {
    let newIndex = i;
    if (this.props.steps && this.props.steps.length > newIndex && newIndex>=0) {
      if (this.props.onChange) {
        this.props.onChange(newIndex);
      }
      this.setState({step: newIndex});
    }
  };

  render() {
    const {classes, t, i18n, steps } = this.props;

    if (!steps) {
      return;
    }

    return <div className={classes.root}>
      {steps.map( (step, i) =>
        <button className={this.state.step===i ? (classes.btn + " " + classes.selected) : (classes.btn + " " + classes.nonSelected)} onClick={()=>{this.handleClick(i)}}>{steps[i]}</button>
      )}
    </div>
  }
}

OffTheShelfPicker.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(OffTheShelfPicker));