export default class ErrorCodes {

  static STATUS_STAGE_UPLOADING = "Error in fetching the file or file is corrupt";
  static STATUS_STAGE_RETRIEVING_FROM_DRIVE = "Google Drive file denied. Please validate it is PUBLICLY shared and less than 100MB";
  static STATUS_STAGE_QUEUE = "Sorry, error while Will start processing soon...";
  static STATUS_STAGE_PROCESSING = "Couldn't process audio";
  static STATUS_STAGE_SECURING_CREDIT = "Sorry, error while Securing credit";
  static STATUS_STAGE_COPYING = "Sorry, error while Optimizing for transcription";
  static STATUS_STAGE_TRANSCRIBING = "Sorry, error while Transcribing";
  static STATUS_STAGE_PERSISTING_RESULTS = "Sorry, error while Persisting results";

  static errorToExplanation = (error) => {
    let msg: String;

    if (error.startsWith(ErrorCodes.STATUS_STAGE_UPLOADING)) {
      msg = "If it was a link - then it usually means it was not a link to a direct file, but rather to a webpage that contains the file. Make sure you link to the file itself.";
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_RETRIEVING_FROM_DRIVE)) {
      msg = "Either the file was over 100MB in size, or the file is not publicly shared. Please note - it is not enough to share with admin@speechlogger.com but rather share with anyone with the link."
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_QUEUE)) {
      msg = "Internal error on our server. Most likely our bad. Sorry.";
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_PROCESSING)) {
      msg = "If you uploaded the file from disk - it usually means the file was corrupt in some way. Was it processed? If yes - simply upload the original recording.";
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_SECURING_CREDIT)) {
      msg = "It means you did not have enough credit in minutes to fulfill the transcription request - as the file was longer than remaining credits permitted. Easy to fix - simply add credits."
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_COPYING)) {
      msg = "Internal error on our server. Most likely our bad. Sorry.";
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_TRANSCRIBING)) {
      msg = "Either an error on our server or on Google's. Probably best to send our support team the file itself via email at admin@speechlogger.com so they can check.";
    } else if (error.startsWith(ErrorCodes.STATUS_STAGE_PERSISTING_RESULTS)) {
      msg = "Internal error on our server. Most likely our bad. Sorry.";
    }

    return msg;
  }
}
