export default class StoreRepo {

  static store;

  static placeholderStore = {
    dispatch: function(action) {
      console.log('fired ', action, 'but store is null');
    }
  }


  static setStore(store) {
    StoreRepo.store = store;
    console.log("store = ", store);
  }

  static getStore = function () {
    return StoreRepo.store!=null ? StoreRepo.store : StoreRepo.placeholderStore;
  }

}