import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {withStyles} from '@material-ui/core/styles';
import {withTranslation} from "react-i18next";

const styles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  appBar: {
    position: 'relative',
  },
  toolbarTitle: {
    flex: 1,
  },
  cardHeader: {
    backgroundColor: theme.palette.grey[200],
  },
  cardPricing: {
    textAlign: 'center',
    justifyContent: 'center',
    alignItems: 'baseline',
    marginBottom: theme.spacing.unit * 2,
  },
  content: {
    fontWeight: "200",
    fontSize: theme.typography.pxToRem(18),
    color: "#444",
    whiteSpace: 'pre-line'
  },
  cardActions: {
    [theme.breakpoints.up('sm')]: {
      paddingBottom: theme.spacing.unit * 2,
    },
  },
  footer: {
    marginTop: theme.spacing.unit * 8,
    borderTop: `1px solid ${theme.palette.divider}`,
    padding: `${theme.spacing.unit * 6}px 0`,
  },
});


function Samples2(props) {
  const {classes, t, i18n } = props;
  const assetsPath = process.env.PUBLIC_URL + '/assets/';


  const tiers = [
    {
      title: t('demos.1.title'),
      audio: 'pro.mp3',
      results: 'pro.mp3_transcript.txt',
      accuracy: 100,
      source: 'https://ttsreader.com/voiceover/',
    },
    {
      title: t('demos.2.title'),
      audio: 'home.aac',
      results: 'home.mp4_transcript.txt',
      accuracy: 97,
      source: 'https://www.youtube.com/watch?v=aCs21PsNjNA',
    },
    {
      title: t('demos.3.title'),
      audio: 'foreign.m4a',
      results: 'foreign.m4a_transcript.txt',
      accuracy: 84,
      source: '',
    },
  ];


  return (
    <React.Fragment>
      <CssBaseline />

      <div className={classes.layout}>
        <div>
          <Typography component="h3" variant="h4" align="center" color="textPrimary">
            {t('demos.title')}
          </Typography>
          <br/>
          <Typography variant="h6" align="center" color="textSecondary" component="p">
            {t('demos.subtitle')}
          </Typography>
        </div>

        <br/><br/>

        <Grid container alignItems="flex-end">
          {tiers.map(tier => (
            // Enterprise card is full width at sm breakpoint
            <Grid item key={tier.title} xs={12} sm={tier.title === 'Enterprise' ? 12 : 6} md={4}>
              <div className={classes.cardHeader}>
                <audio style={{width: "100%"}} controls preload="metadata"
                       src={assetsPath + tier.audio}>
                </audio>
              </div>
              <Card data-aos={"flip-left"}>
                <CardContent>
                  <div className={classes.cardPricing}>
                    <Typography className={classes.content} color="textSecondary">
                      {tier.title}
                    </Typography>
                  </div>
                  <div className={classes.cardPricing}>
                    <Typography variant="h6" color="textPrimary" style={{}}>
                      {tier.accuracy.toString() + "%"}
                    </Typography>
                    <Typography variant="h6" color="textSecondary">
                      {t('accuracy')}
                    </Typography>
                  </div>
                </CardContent>

                <CardActions className={classes.cardActions}>
                  <Button fullWidth variant='outlined' color="primary" onClick={()=>{window.open(assetsPath+tier.results, "_blank");}}>
                    {t('results')}
                  </Button>
                </CardActions>

              </Card>
            </Grid>
          ))}
        </Grid>
      </div>

    </React.Fragment>
  );
}

Samples2.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withTranslation()(Samples2));