import {connect} from 'react-redux'
import React from "react";
import {setUserPrefPrintSpeakersAction, setUserPrefPrintTimesAction} from "../actions";

// This function takes a component...
export function withPrefs(WrappedComponent) {

  const mapStateToProps = (state, props) => ({
      userPrefs: {
        withTimes: state.prefTimesReducer,
        withSpeakerTags: state.prefSpeakersReducer,
      },
      ...props,
  });

  const mapDispatchToProps = dispatch => ({
    setWithTimes: (yesNo) => dispatch(setUserPrefPrintTimesAction(yesNo)),
    setWithSpeakerTags: (yesNo) => dispatch(setUserPrefPrintSpeakersAction(yesNo)),
  });

  // ...and returns another component...
  return connect(
    mapStateToProps,
    mapDispatchToProps
  )(WrappedComponent);
}
