import {Api} from "../network/Api";
import Helpers from "../utils/Helpers";

export default class UploadFileJob {

  jobId:number = NaN;
  file:File = null;
  fileUrl:String;
  language:String;
  name:String;
  model:String;
  numSpeakers:number = 2;
  duration:number = NaN;
  progress:number = NaN;  // NaN did not start, 0 started, 1 finished successfully, -1 failed

  constructor(file: File, fileUrl:String, name: String, language:String, model: String, duration: number, numSpeakers: number) {
    this.file = file;
    this.fileUrl = fileUrl;
    this.name = name;
    this.language = language;
    this.model = model;
    this.duration = duration;
    this.numSpeakers = numSpeakers;
  }



  upload(onSuccessCall, onFailCall, onProgressCall) {
    this.progress = 0; // To annotate: we started -> block the upload queue until released.

    let onSuccess = () => {
      if (onSuccessCall!=null) {
        onSuccessCall();
      }

      this.progress = 1;
    }

    let onFail = () => {
      if (onFailCall!=null) {
        onFailCall();
      }

      this.progress = -1;
    }

    let onProgress = (uploadedBytes, totalBytes) => {
      let progress = uploadedBytes/totalBytes;

      if (Number.isNaN(progress) || progress<=0 || progress>=1 ) {
        return;
      }

      if (onProgressCall!=null) {
        onProgressCall(progress);
      }

      this.progress = progress;
    }

    if (this.fileUrl && this.fileUrl.includes("https://youtube.com/")) {
      // Fix youtube url format:
      this.fileUrl = this.fileUrl.replace("https://youtube.com/","https://www.youtube.com/");
    }
    Api.uploadFile(this.file,this.fileUrl, this.language, this.numSpeakers, this.model, onSuccess, onFail, onProgress);
  }

}